const current = {
    // 转为yy-mm-dd hh-mm-ss格式
    formatDateTime: function (inputTime) {
        let date = new Date(inputTime);
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let minute = date.getMinutes();
        let second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '/' + m + '/' + d + ' ' + h + ':' + minute + ':' + second;
    },
    // 转成hh-mm-ss格式
    formaMinuteTime: function (inputTime) {
        let date = new Date(inputTime);
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let minute = date.getMinutes();
        let second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return  h + ':' + minute + ':' + second;
    },
    getTimestamp: function (date) {
        let time = Date.parse(date.replace(/-/g, '/'));
        return time
    },
    // 获取本分钟的第一秒的时间戳  接收时间戳 返回这一分钟初始时间戳
    getMinuteStartTime (inputTime){
        const index = new Date(inputTime).getSeconds()
        const millSecond = inputTime.toString().substr(-3, 3);
        return inputTime - index * 1000 - millSecond
    },
    // 获取本分钟的最后一秒的时间戳
    getMinuteEndTime (inputTime){
        const startTime = this.getMinuteStartTime(inputTime)
        return  startTime + 60 * 1000
    },
    // 目前时间距离最后一秒还有多少秒
    getMinuteFromLastTime (inputTime){
        const index = new Date(inputTime).getSeconds()
        return 60 - index
    }
}

export default current