<template>
    <div>
        <div class="mobile-show">
            <mobileHeader :returnPath="'/account'" :title="$t('main.AccountPage.ChangePassword')"></mobileHeader>
        </div> 
        <div class="change-password-page asset-account-container">
            <div class="sidebar">
                <navSidebar :menuItems="menuList" :sidebarTitle="sidebarTitle" :selectedItem="selectedItem"></navSidebar>
            </div>
            <div class="main-content">
                <div class="heading">{{$t('main.AccountPage.ChangePassword')}}</div>
                <div class="form-container">
                    <el-form class="password-form">
                        <el-form-item prop="username" class="form-group responsive-input">
                            <div class="mobile-show label">
                                {{$t('main.ChangePasswordPage.CurrentPassword')}}
                            </div>
                            <el-input
                                class="login-inputorbuttom"
                                :placeholder="$t('main.ChangePasswordPage.CurrentPassword')"
                                suffix-icon="el-icon-view"
                                v-model.trim="passwordData.currentPassword"></el-input>
                        </el-form-item>
                        <el-form-item prop="username" class="form-group responsive-input">
                            <div class="mobile-show label">
                                {{$t('main.ChangePasswordPage.NewPassword')}}
                            </div>
                            <el-input
                                class="login-inputorbuttom"
                                :placeholder="$t('main.ChangePasswordPage.NewPassword')"
                                suffix-icon="el-icon-view"
                                v-model.trim="passwordData.newPassword"></el-input>
                        </el-form-item>
                        <el-form-item prop="username" class="form-group responsive-input">
                            <div class="mobile-show label">
                                {{$t('main.ChangePasswordPage.ConfirmNewPassword')}}
                            </div>
                            <el-input
                                class="login-inputorbuttom"
                                :placeholder="$t('main.ChangePasswordPage.ConfirmNewPassword')"
                                suffix-icon="el-icon-view"
                                v-model.trim="passwordData.confirmNewPassword"></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="submit-button">
                        <el-button
                            class="responsive-button"
                            type="primary"
                        >{{$t('main.AccountPage.ChangePassword')}}</el-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
import { mapState } from "vuex";
import navSidebar from "@/components/navSidebar";
import { NavigationItems } from '@/common/navigation-items.js'
import mobileHeader from '@/components/mobileHeader';

export default {
    name: "changePasswordPage",
    data() {
        return {
            isLoggedIn: false,
            passwordData: {},
            sidebarTitle: NavigationItems.userSettingsTitle,
            menuList: NavigationItems.userSettings,
            selectedItem: 'main.AccountPage.ChangePassword'
        };
    },
    computed: { },
    created() {
        let token = this.$common.getCookie('token');
        if(token) {
            this.getUserAccount()
        }
    },
    mounted() {

    },
    components: {
        navSidebar,
        mobileHeader
    },
    methods: {
        getUserAccount (){
            this.$http.userDetails().then((res) => {
                this.isLoggedIn = true;
                if(res && res.data){
                    this.userInfo = res.data.data;
                }
            });
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
    }
};
</script>

<style lang="scss">
    .change-password-page {
        .form-container {
            font-size: 14px;
            max-width: 500px;
        }
        .submit-button {
            margin-top: 50px;
            text-align: center;
        }
        @media screen and (max-width: 768px) {
            .form-container {
                width: 90%;
                margin: auto;
            }
        }
    }
    
</style>