<template>
    <table>
        <thead>
            <tr>
                <th>{{$t('main.historyPage.date')}}</th>
                <th>{{$t('main.OrderRecord.Position')}}</th>
                <th>{{$t('main.OrderRecord.RemainingTime')}}</th>
            </tr>
        </thead>
        <tbody>
            <template v-for="(item, i) in tradeItems">
                <tr :key="i" @click="showDetails(item)">
                    <td class="index-type" colspan="3">{{item.marketCode}}</td>
                </tr>
                <tr :key="i+'a'" @click="showDetails(item)">
                    <td class="date">{{item.createTime}}</td>
                    <td>
                        <div v-bind:class="item.marketPrice - item.nowPrice > 0  ? 'direction up' : 'direction down'">
                            {{item.marketPrice - item.nowPrice > 0  ? $t('main.OrderRecord.up') : $t('main.OrderRecord.down')}}
                        </div> {{item.tradePrice / 100}}</td>
                    <td class="time-remaining">
                        {{item.timeRemaining}} 
                        <div class="icon-container">
                            <i v-if="!item.showDetails" class="el-icon-arrow-down"></i>
                        </div>
                    </td>
                </tr>
                <tr :key="i+'b'" v-bind:style="{'border-bottom': item.percentageNumber ? 'none' : '1px solid #787B89'}" @click="showDetails(item)">
                    <td colspan="3" class="details">
                        <div class="price-difference">
                            <div class="previous-amount">{{item.marketPrice}}</div>
                            <i class="el-icon-right"></i> <i v-if="!history" class="el-icon-time"></i> 
                            <span v-if="!history" v-bind:class="item.marketPrice - item.nowPrice > 0  ? 'up' : 'down'">{{item.nowPrice}}</span>
                            <span v-else>{{item.remark}}</span>
                        </div>
                        <div v-if="item.showDetails">
                            <div class="details-split">
                                <div>{{$t('main.OrderRecord.Time')}}: 1m</div>
                                <div v-if="history">{{$t('main.priceList.income')}}: {{item.settlePriceYuan}} <div class="icon-container"></div></div>
                            </div>
                            <div v-if="!history" class="details-split" style="display: block;">
                                <div>{{$t('main.OrderRecord.Start')}}: {{new Date(item.initTimeStamp).toLocaleString()}}</div>
                                <div>{{$t('main.OrderRecord.End')}}: {{new Date(item.endTimeStamp).toLocaleString()}} <div class="icon-container"></div></div>
                            </div>
                            <div class="id details-split">
                                <div>{{$t('main.OrderRecord.OrderID')}}: {{item.tradeId}}</div>
                                <i class="el-icon-arrow-up"></i>
                            </div>
                        </div>
                        <!-- <div v-if="!history" class="progress-bar-container">
                            <div class="progress-bar" v-bind:style="{width: item.percentTimeLeft*100 + '%'}"></div>
                        </div> -->
                        <div v-if="!history && item.percentageNumber > 0">
                            <el-progress :percentage="item.percentageNumber" :show-text="false"  style="padding-right:20px"></el-progress>
                        </div>
                    </td>
                </tr>
            </template>
        </tbody>
    </table>
</template>

<script>

export default {
    name: "TradeTable",
    components: {
    },
    data() {
        return {
            emitTimer: null
        }
    },
    props: {
        tradeItems: {
            type: Array,
            default: []
        },
        history: {
            type: Boolean,
            default: false
        }
    },
    computed: {

    },
    watch: {
        tradeItems: function (data){
            this.resetCountdown(data)
        }
    },
    created() {},
    destroyed() {
    },
    mounted() {
    },
    methods: {
        showDetails(item){
            item.showDetails = !item.showDetails;
            this.$forceUpdate();
        },
        clearInterval (){
            if(this.timer){
                clearInterval(this.timer);
            }
        },
        resetCountdown (data){
            this.clearInterval()
            this.timer = setInterval(() => {
                let isResetStatus = false
                data.forEach(v => {
                    let suspensionEndTime = v.endTimeStamp;  //终点时间
                    if(suspensionEndTime){
                        let timeStamp = suspensionEndTime - Date.parse(new Date())
                        if(timeStamp < 0){
                            v.countdown = "00：00：00";
                            v.percentageNumber = 0
                            return false
                        }
                        if(timeStamp === 0){
                            isResetStatus = true
                        }
                        let date = new Date(timeStamp);
                        let m = date.getMinutes() ; //获取分钟数(0-59)
                        let s = date.getSeconds() ; //获取秒数(0-59)
                        v.minutes = m > 5 ? 0 : m;
                        v.seconds = s;
                        if (m < 10) {
                            m = "0" + m;
                        }
                        if (s < 10) {
                            s = "0" + s;
                        }
                        v.countdown = "00：" + m + "：" + s;

                        let minute = Number(v.tradeType.split('_')[1].split('')[0]);//当前所在分秒区
                        let total = (Number(v.minutes) * 60) + Number(v.seconds);//当前总数
                        let totalNumber = minute * 60;//总数
                        let remaining = totalNumber - total;
                        v.percentageNumber = remaining / totalNumber * 100;
                    }else{
                        v.countdown = "00：00：00";
                        v.percentageNumber = 0
                    }
                });

                // 倒计时结束通知父组件刷新订单，因为可能订单结束瞬间并未结算完，所以延迟2秒
                if(isResetStatus){
                    if(!this.emitTimer){
                        this.emitTimer = setTimeout(() => {
                            this.$emit('getUserOrders')
                        }, 2000)
                    }
                }
            }, 1000)
        }
    }
};
</script>

<style lang='scss' scoped>
    table {
        font-size: 13px;
        color: #DDDEE5;
        width: 100%;
        i {
            cursor:pointer;
        }
        tr {
            td, th {
                
                text-align: center;
                &:first-of-type {
                text-align: left;
                }
                &:last-of-type {
                    text-align: right;
                }
            }

            th {
                padding: 3px 10px;
                border-top: 1px solid #787B89;
                border-bottom: 1px solid #787B89;
            }

            td {
                padding: 4px 10px;
            
                .icon-container {
                    display: inline-block;
                    width: 20px;
                    height: 12px;
                    line-height: 16px;
                }
                &.date {
                    color: #787B89;
                }
                &.details, &.index-type {
                    padding-top: 8px;
                    text-align: left;
                }

                &.details {
                    color: #787B89;
                    div {
                        padding: 2px 0;
                    }

                    .price-difference {
                        .previous-amount {
                            vertical-align: bottom;
                            display: inline-block;
                            width: 80px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                        i {
                            font-weight: bold;
                            margin-right: 5px;

                            &:last-of-type {
                                margin-right: 10px;
                            }
                        }
                    }

                    .progress-bar-container {
                        margin-top: 3px;
                        width: 100%;
                        height: 6px;
                        background-color: #3b3f4d;
                        padding: 0;
                        .progress-bar {
                            height: 2px;
                            background-color: #04affe;
                        }
                    }

                    .details-split {
                        display: flex;
                        justify-content: space-between;
                        .end-time {
                            padding-right: 10px;
                        }
                    }
                }

                &.time-remaining {
                    i {
                        margin-left: 6px;
                    }
                }

                .direction {
                    display: inline-block;
                    width: 35px;
                    padding-right: 8px;
                    text-align: right;
                }
            }
        }
    }
</style>