<template>
    <div class="bussinessChartPopUps_box">
        <div id="bussinessChartPopUps" ref="bussinessChartPopUps"></div>
        <!-- 当前价格 -->
        <div :style="{top: startY+'px', left: startX + 'px'}" class="position-a endBorders">
            <div>{{this.DataList[this.DataList.length - 1][1]}}</div>
        </div>
        <!-- 定位logo -->
        <div class="position-a logo_box"></div>
    </div>
</template>

<script>
var echarts = require("echarts");
import { mapState } from "vuex";
export default {
    name: "bussinessChartPopUps",
    data() {
        return {
            startY: 0,
            startX: 0,
            DataList: [
                [],
                []
            ], //数据
            currentIconChartName: null,
            // placeAnOrderList: [[1575887401000, 1], [1575887403000, 1]] //下单标注点
            placeAnOrderList: [], //下单标注点
            myChart: null,
            chartResultPeriod: '1M'
        };
    },
    computed: {
        ...mapState({
            sceenWidth: state => state.sceenWidth
        }),
        markPointData() {
            let obj = [];
            const buyInfoCookie = this.$common.getCookie("currentBuyInfo");
            if(buyInfoCookie){
                let finalCurrentInfo = JSON.parse(buyInfoCookie)[`${this.chartResultPeriod}-${this.currentIconChartName}`]
                obj = [...obj, ...finalCurrentInfo]
            }
            let endCallout = {
                //终点标注点
                symbol: "circle",
                symbolSize: "10",
                label: {
                    normal: {
                        show: false
                    },
                    fontSize: 10
                },
                itemStyle: {
                    color: "#fff"
                },
                coord: [this.DataList[this.DataList.length - 1][0], this.DataList[this.DataList.length - 1][1]]
            };
            obj.push(endCallout);
            return obj;
        },
        markLineData() {
            let deadCount = this.chartResultPeriod === '1M' ? 9 : 29
            let arr = [["#439957", deadCount], ["red", 0]]
            let brr = [];
            arr.forEach(item => {
                let lineStyleObj = {
                    lineStyle: {
                        type: "solid",
                        color: {
                            type: "linear",
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: "#273042" // 0% 处的颜色
                                },
                                {
                                    offset: 1,
                                    color: item[0] // 100% 处的颜色
                                }
                            ],
                            global: false // 缺省为 false
                        },
                        width: 1
                    },
                    xAxis: new Date(this.DataList[this.DataList.length - 1][0]).getTime() - item[1] * 1000
                };
                brr.push(lineStyleObj);
            });
            return brr;
        }
    },
    created() {
    },
    mounted() {
        this.myChart = echarts.init(
            document.getElementById("bussinessChartPopUps")
        );
        window.addEventListener("resize", (e) => {
            this.myChart.resize();
        });
    },
    watch: {},
    methods: {
        resetChart(DataList, currentIconChartName, chartResultPeriod) {
            this.DataList = DataList
            if(currentIconChartName){
                var currentIconChartNameShow = currentIconChartName.split('-')[1]
                this.currentIconChartName = currentIconChartName
            }
            this.chartResultPeriod = chartResultPeriod
            function add0(m) {
                return m < 10 ? "0" + m : m;
            }

            let splitNumber
            if(this.sceenWidth > 768){
                splitNumber = chartResultPeriod === '1M' ? 20 : 10
            }else{
                splitNumber = 5
            }

            let option = {
                animationDuration: 0,
                backgroundColor: "#273042",
                title: {
                    text: `${this.$t("main.guideMap.subtext")}(${currentIconChartNameShow})`,
                    left: "center",
                    top: "10",
                    textStyle: {
                        color: "#fff",
                        fontSize: this.sceenWidth > 768 ? 16 : 12
                    }
                },
                grid: [
                    {
                        left: "10%",
                        bottom: "10%",
                        top: "20%",
                        right: "10%"
                    }
                ],
                xAxis: {
                    type: "time",
                    boundaryGap: false,
                    splitLine: {
                        show: false
                    },
                    minInterval: 1,
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    axisLabel: {
                        textStyle: {
                            fontSize: 10,
                            color: "#a0a0c1"
                        }
                    },
                    splitNumber: splitNumber,
                    splitArea: false
                },
                yAxis: {
                    type: "value",
                    min: function(value) {
                        return value.min - 0.02;
                    },
                    max: function(value) {
                        return value.max + 0.02;
                    },
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    splitLine: {
                        show: false
                    },
                    axisLabel: {
                        formatter: function(value, index) {
                            return value.toFixed(2);
                        },
                        textStyle: {
                            fontSize: 10,
                            color: "#a0a0c1"
                        }
                    }
                },
                series: [
                    {
                        type: "line",
                        symbol: "none",
                        data: this.DataList,
                        areaStyle: {
                            //线性填充区域颜色
                            normal: {
                                color: new echarts.graphic.LinearGradient(
                                    0,
                                    0,
                                    0,
                                    1,
                                    [
                                        {
                                            offset: 0,
                                            color: "#DDDDDD" // 0% 处的颜色
                                        },
                                        {
                                            offset: 1,
                                            color: "rgba(0,0,0,0)" // 100% 处的颜色
                                        }
                                    ]
                                )
                            }
                        },
                        lineStyle: {
                            //折现的颜色设置}
                            normal: {
                                color: "#fff",
                                width: 1
                            }
                        },
                        markLine: {
                            //标注线的设置
                            silent: true,
                            symbol: "none",
                            label: {
                                show: true,
                                normal: {
                                    show: true,
                                    textStyle: {
                                        fontSize: 10,
                                        color: "#439957"
                                    },
                                    formatter: function(params) {
                                        let time = new Date(params.value);
                                        var h = time.getHours();
                                        var mm = time.getMinutes();
                                        var s = time.getSeconds();
                                        return (
                                            add0(h) +
                                            ":" +
                                            add0(mm) +
                                            ":" +
                                            add0(s)
                                        );
                                    }
                                }
                            },
                            data: this.markLineData
                        },
                        markPoint: {
                            //标注点的设置
                            symbolSize: 30,
                            silent: true,
                            data: this.markPointData
                        }
                    }
                ]
            };
            this.myChart.setOption(option);
            let _points = this.myChart.getViewOfSeriesModel(
                this.myChart.getModel().getSeries()[0]
            )._points;
            this.startY = _points[_points.length - 1][1] - 8;
            this.startX = _points[_points.length - 1][0] + 6;
        }
    },
    components: {}
};
</script>
<style scoped lang="scss">
.bussinessChartPopUps_box {
    width: 100%;
    position: relative;
    #bussinessChartPopUps {
        width: 780px;
        height: 300px;
    }
    .position-a {
        position: absolute;
    }
    .endBorders {
        z-index: 11;
        text-align: center;
        width: 65px;
        div {
            width: 100%;
            background: #494869;
            z-index: 3;
            color: #fff;
            font-size: 10px;
            line-height: 16px;
            white-space: nowrap;
        }
    }
    .logo_box {
        width: 150px;
        height: 26px;
        top: 10px;
        left: 30px;
        background: url("../assets/okbix-logo-small.png") no-repeat;
        background-size: auto 100%;
    }
}
@media screen and (max-width: 768px) {
    .bussinessChartPopUps_box{
        #bussinessChartPopUps {
            width: 97%;
            height: 160px;
        }
        .endBorders{
            width: 60px;
        }
        .logo_box{
            top: 6px;
            left: 5px;
            height: 13px;
        }
    }
}
</style>
