<!--
 * @Descripttion: 登录界面
 * @Author: wangxi
 * @Date: 2019-11-29 03:30:31
 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-16 15:19:16
 -->
<template>
    <div class="login-container" v-bind:class="{'qr-active':showQr}">
        <div id="login">
            <div class="login-registration-header">
                <div @click="returnHome">
                    <i v-if="!showQr" class="el-icon-close"></i>
                    <div v-if="showQr" class="title">
                        {{$t('main.LoginRegistration.Login')}}
                    </div>
                </div>
                <div @click="switchLang" class="lang">{{$t('main.nav.LanguageaSwitching')}}</div>
            </div>
            
            <div v-if="!showQr" class="qr-icon">
                <img src="../../assets/icons/qr_icon.svg" alt="" @click="toggleShowQr()">
            </div>
            <el-form class="loginFrom" :model="logindata" ref="ruleForm">
                <el-form-item v-if="showQr" class="login-item">
                    <div class="qr-container">
                        <div class="qr-close mobile-hide"><i @click="toggleShowQr()" class="el-icon-close"></i></div>
                        <div class="qr-label">{{$t('main.LoginRegistration.QrLogin')}}</div>
                        <img class="qr-code" src="../../assets/qr-sample.png" alt />
                        <pre>{{$t('main.LoginRegistration.QrMessage')}}</pre>
                    </div>
                </el-form-item>
                <template v-if="!showQr">
                    <el-form-item class="login-item">
                        <img class="logo" src="../../assets/okbix-logo-vertical.png" alt />
                    </el-form-item>
                    <el-form-item prop="username" class="form-group responsive-input">
                        <div
                            class="mobile-show label"
                        >{{$t('main.LoginRegistration.Username')+'/'+$t('main.LoginRegistration.Email')}}</div>
                        <el-input
                            class="login-inputorbuttom"
                            prefix-icon="el-icon-user"
                            :placeholder="$t('main.LoginRegistration.Username')+'/'+$t('main.LoginRegistration.Email')"
                            v-model.trim="logindata.username"
                        ></el-input>
                    </el-form-item>
                    <el-form-item prop="password" class="form-group responsive-input" style="position: relative;">
                        <div class="mobile-show label">{{$t('main.LoginRegistration.Password')}}</div>
                        <el-input
                            :type="passwordType"
                            class="login-inputorbuttom"
                            prefix-icon="el-icon-lock"
                            :placeholder="$t('main.LoginRegistration.Password')"
                            v-model.trim="logindata.password"
                        ></el-input>
                        <span v-if="logindata.password" class="password-icon" @click="switchPasswordType">
                            <i v-if="passwordType === 'password'" class="icon-visibility_off"></i>
                            <i v-else class="icon-remove_red_eye"></i>
                        </span>
                    </el-form-item>
                </template>
                <el-form-item class="login-item">
                    <vaptcha @success="validSuccess"></vaptcha>
                </el-form-item>
                <el-form-item class="login-item" v-if="isDemo">
                    <el-button
                        class="login-inputorbuttom login-bottom responsive-button"
                        @click="demoLogin()"
                    >{{$t('main.LoginRegistration.DemoAccess')}}</el-button>
                </el-form-item>
                <el-form-item v-if="showQr" class="login-item mobile-show">
                    <el-button
                        class="login-inputorbuttom login-bottom"
                        @click="showQr=false"
                    >{{$t('main.LoginRegistration.StandardLogin')}}</el-button>
                </el-form-item>
                <el-form-item class="login-item">
                    <el-button
                        v-bind:class="{'responsive-button':!isDemo}"
                        class="login-inputorbuttom login-bottom"
                        @click="isDemo ? toggleIsDemo() : slowLogin()"
                    >{{$t('main.LoginRegistration.Login')}}</el-button>
                </el-form-item>
                <el-form-item class="login-item" v-if="!isDemo">
                    <el-button
                        class="login-inputorbuttom login-bottom"
                        @click="forgotPassword"
                    >{{$t('main.LoginRegistration.ForgotPassword')}}</el-button>
                </el-form-item>
                <el-form-item class="login-item">
                    <el-button
                        class="login-inputorbuttom login-bottom"
                        @click="jumpRegister"
                    >{{$t('main.LoginRegistration.Register')}}</el-button>
                </el-form-item>
                <el-form-item v-if="!isDemo" class="login-item">
                    <el-button
                        class="login-inputorbuttom login-bottom"
                        @click="toggleIsDemo()"
                    >{{$t('main.LoginRegistration.DemoAccess')}}</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import vaptcha from "@/components/vaptcha.vue";
export default {
    name: "login",
    data() {
        return {
            logindata: {
                username: "",
                password: "",
                ip: null
            },
            validStatus: false,
            slowLogin: null,
            timer: null,
            showQr: this.$route.query.showQr == 'true',
            isDemo: this.$route.query.isDemo == 'true',
            passwordType: 'password'
        };
    },
    components: {
        vaptcha
    },
    created() {
        this.slowLogin = this.$common.debounce(this.submitLogin);
        const username = this.$route.query.username;
        if (username) {
            this.logindata.username = username;
        }

        // 获取ip的js
        var script = document.createElement("script");
        script.src = "http://pv.sohu.com/cityjson?ie=utf-8";
        script.defer = "defer"
        document
            .getElementsByTagName("head")[0]
            .appendChild(script);
    },
    mounted (){
        // 异步mounted阶段可能会获取不到ip，循环去获取
        this.timer = setInterval(() => {
            if(window.returnCitySN && window.returnCitySN["cip"]){
                clearInterval(this.timer)
                this.logindata.ip = window.returnCitySN["cip"]
            }
        }, 1000)
    },
    methods: {
        switchPasswordType (){
            this.passwordType = this.passwordType === 'password' ? 'text' : 'password'
        },
        jumpRegister() {
            this.$router.push({
                name: "register"
            });
        },
        forgotPassword() {
            this.$router.push({
                name: "change-password"
            });
        },
        // 人机验证成功
        validSuccess(token) {
            if (token) {
                this.validStatus = true;
            }
        },
        switchLang() {
            this.$i18n.locale = this.$i18n.locale == "zh" ? "en" : "zh";
        },
        returnHome() {
            this.$router.push({
                name: "homeSection"
            });
        },
        toggleIsDemo() {
            this.$router.push({path: 'login', query: {
                isDemo: !this.isDemo,
                showQr: this.showQr
            }});
            
            this.isDemo = !this.isDemo;

        },
        toggleShowQr() {
            this.$router.push({path: 'login', query: {
                isDemo: this.isDemo,
                showQr: !this.showQr
            }});

            this.showQr = !this.showQr;
        },
        demoLogin() {
            this.$store.commit(
                "setLoading",
                true
            )

            setTimeout(() => {
                this.$store.commit(
                    "setLoading",
                    false
                )
                this.$router.push({
                    name: "homeSection"
                });
            }, 1800);
        },
        submitLogin() {
            if(!this.logindata.ip){
                this.$$message(this.$t('main.tip.laterAgain'))
                return false
            }
            
            if (!this.logindata.username || !this.logindata.password) {
                this.$message(this.$t('main.tip.passwordcannotbeempty'));
                return false;
            }

            // 最短6位，最长16位
            // 必须包含1个数字
            // 必须包含1个小写字母
            // 必须包含1个大写字母
            // 必须包含1个特殊字符
            const passwordRegx = /^.*(?=.{6,16})(?=.*\d)(?=.*[A-Z]{1,})(?=.*[a-z]{1,})(?=.*[!@#$%^&*?\(\)\.]).*$/;
            if (!passwordRegx.test(this.logindata.password)) {
                this.$message(this.$t('main.tip.specialcharacters'));
                return false;
            }

            if (!this.validStatus) {
                this.$message(this.$t('main.tip.verifyfirst'));
                return false;
            }

            this.$http.login(this.logindata).then(res => {
                if (res) {
                    this.$message(this.$t('main.tip.loginsuccessfulaaa'));
                    this.$common.setCookie("token", res.data.data.token);
                    this.$common.setCookie("dialogVisible", 1);
                    setTimeout(() => {
                        const redirect = this.$route.query.redirect;
                        if (redirect) {
                            this.$router.push({
                                path: redirect
                            });
                        } else {
                            this.$router.push({
                                name: "homeSection"
                            });
                        }
                    }, 500);
                }
            });
        }
    },
    destroyed() {
        // 离开页面时隐藏掉弹窗
        const popEl = document.getElementsByClassName("vaptcha-popup");
        document.getElementsByClassName("vaptcha-popup")[
            popEl.length - 1
        ].style.display = "none";
        // 清除定时器
        clearInterval(this.timer)
    }
};
</script>

<style lang="scss">
.password-icon{
    position: absolute;
    right: 15px;
    bottom: 0;
    color: #C0C4CC;
}

.login-container {
    min-height: fit-content;
    height: 100vh;
    width: 100vw;
    @media screen and (min-width: 769px) { 
        &.qr-active {
            background-color: #101219;
        }
    }
}
#login {
    padding-top: 50px;
    text-align: center;
    margin: auto;
    max-width: 500px;
    width: calc(100vw - 50px);

    .login-item {
        margin-bottom: 10px;
    }

    .title {
        text-align: left;
        color: #dddee5;
        margin-bottom: 20px;
    }

    .login-registration-header {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 5px;
        line-height: 27px;
        i {
            cursor: pointer;
            color: #787b89;
            font-size: 26px;
        }
        .lang {
            color: #04affe;
            cursor: pointer;
        }
    }

    .qr-icon {
        text-align: right;
        cursor: pointer;
    }

    #vaptchaContainer {
        display: inline-block;
        margin-top: 25px;
    }

    .el-form {
        width: 100%;

        .qr-container {
            width: 100%;
            padding-bottom: 30px;
            background-color: #202432;
            color: #dddee5;
            .qr-close {
                padding: 10px 10px 0;
                text-align: right;
                line-height: 10px;
                cursor: pointer;
            }
            .qr-label {
                font-size: 16px;
            }

            .qr-code {
                height: 250px;
                margin: 20px;
            }

            .qr-message {
                line-height: 20px;
            }
        }

        .logo {
            width: 160px;
        }

        button.el-button {
            background-color: transparent;
            border: none;
            color: #04affe;
        }
    }

    @media screen and (max-width: 768px) {
        padding-top: 20px;
        .el-form {
            .form-group {
                color: #dddee5;
                text-align: left;
                .label {
                    height: 35px;
                    margin-left: 2px;
                }
            }
            .el-input__prefix {
                display: none;
            }
        }
    }
}
</style>