<template>
    <div>
        <div class="mobile-show">
            <mobileHeader  :returnPath="'/account'" :title="$t('main.AccountPage.FA')"></mobileHeader>
        </div> 
        <div class="asset-account-container">
            <div class="sidebar">
                <navSidebar :menuItems="menuList" :sidebarTitle="sidebarTitle" :selectedItem="selectedItem"></navSidebar>
            </div>
            <div class="main-content">
                <div class="heading">{{$t('main.AccountPage.FA')}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import navSidebar from "@/components/navSidebar";
import { NavigationItems } from '@/common/navigation-items.js'
import mobileHeader from '@/components/mobileHeader';

export default {
    name: "faPage",
    data() {
        return {
            isLoggedIn: false,
            userInfo: {},
            sidebarTitle: NavigationItems.userSettingsTitle,
            menuList: NavigationItems.userSettings,
            selectedItem: 'main.AccountPage.FA'
        };
    },
    computed: { },
    created() {
        let token = this.$common.getCookie('token');
        if(token) {
            this.getUserAccount()
        }
    },
    mounted() {

    },
    components: {
        navSidebar,
        mobileHeader
    },
    methods: {
        getUserAccount (){
            this.$http.userDetails().then((res) => {
                this.isLoggedIn = true;
                if(res && res.data){
                    this.userInfo = res.data.data;
                }
            });
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
    }
};
</script>

<style lang="scss">
    @media screen and (max-width: 768px) {}
    
</style>