<script>
import { mapState } from "vuex";
import GeneralPurpose from "@/components/generalPurpose";
import Tutorial from "@/components/Tutorial";
import SockJS from "sockjs-client";
import Stomp from "stompjs";
export default {
    name: "homeSection",
    props: {},
    data() {
        return {
            isShow: false,
            isGetData: false,
            tableData: [],
            input2: "",
            activeName: "Collection",
            transformIconAjaxData: {
                collection: [],
                product: [],
                index: [],
                stock: []
            },
            collecList: [],
            dataList: [],
            searchText: "",
            searchFun: null,
            stompClient: null
        };
    },
    components: {
        GeneralPurpose,
        Tutorial
    },
    computed: {
        ...mapState({
            allIconNowPrice: state => state.allIconNowPrice,
            cloneHomeIconDetailList: state => state.cloneHomeIconDetailList
        })
    },
    created() {
        this.getData();
        this.initSocketJs()
        // 搜索防抖
        this.searchFun = this.$common.debounce(this.filterIconData, 500);
    },
    mounted() {
        introJs()
            .setOptions({
                nextLabel: "下一步",
                prevLabel: "上一步",
                skipLabel: "跳过",
                doneLabel: "结束"
            })
            .oncomplete(function() {
                //点击跳过按钮后执行的事件
            })
            .onexit(function() {
                //点击结束按钮后， 执行的事件
            })
            .start();
    },
    watch: {},
    beforeDestroy (){
        this.disconnect();
    },
    methods: {
        iconSearch(e) {
            this.searchFun();
        },
        // 本地搜索
        filterIconData() {
            let returnFilterIconData = this.cloneHomeIconDetailList.filter(v =>
                v.code.toUpperCase().includes(this.searchText.toUpperCase())
            );
            this.$store.commit("setCloneHomeIconDetailList", returnFilterIconData);
        },
        initSocketJs() {
            this.connection();
        },
        connection() {
            // 建立连接对象
            let socket = new SockJS(this.$base.websocketUri);
            // 获取STOMP子协议的客户端对象
            this.stompClient = Stomp.over(socket);
            this.stompClient.debug = null;
            this.stompClient.connect(
                {},
                data => {
                    this.stompClient.subscribe("/pic/all", response => {
                        this.$store.commit(
                            "setAllIconNowPrice",
                            JSON.parse(response.body)
                        );

                        // 数组修改里面的属性需要重新赋值视图才刷新
                        let tempArr = [...this.tableData]
                        tempArr.forEach(v => {
                            if (this.allIconNowPrice) {
                                v.nowPrice = this.allIconNowPrice[v.code];
                            }
                        });
                        this.tableData = tempArr
                    });
                },
                err => {
                    // 连接发生错误时的处理函数
                    console.log("失败");
                    console.log(err);
                }
            );
        },
        disconnect() {
            if (this.stompClient) {
                this.stompClient.disconnect();
            }
        },
        getData(indexs) {
            let tab_index = {
                index: indexs || 0
            };
            this.$http.currencyForm().then(res => {
                this.isGetData = true;
                let result = res.data.data;
                this.dataList = result;
                result.forEach(msg => {
                    if (!msg.collec) {
                        msg.collec = 0;
                    }
                });
                this.transformIconAjaxData.product = result.filter(
                    v => v.market === "fx"
                );
                this.transformIconAjaxData.index = result.filter(
                    v => v.market === "1"
                );
                this.transformIconAjaxData.stock = result.filter(
                    v => v.market === "2"
                );
                let collecList = [];
                let token = this.$common.getCookie("token");
                if (!token) {
                    return false;
                }
                if (this.$common.getCookie("dialogVisible")) {
                    this.dialogVisible = true;
                }
                this.$http.userDetails().then(res => {
                    if (res) {
                        collecList = res.data.data.favorite;
                        this.dataList.forEach(item => {
                            collecList.forEach(items => {
                                if (item.code == items.code) {
                                    item.collec = items.status;
                                }
                            });
                        });
                        this.transformIconAjaxData.collection = this.dataList.filter(
                            v => v.collec == 1
                        );
                        this.handleClick(tab_index);
                    }
                });
            });
        },
        handleClick(e) {
            let index = e.index;
            let data = this.transformIconAjaxData;
            if (this.activeName == "Collection") {
                index = 0;
            }
            if (index == 0) {
                this.tableData = data.collection;
            } else if (index == 1) {
                this.tableData = data.product;
            } else if (index == 2) {
                this.tableData = data.index;
            } else if (index == 3) {
                this.tableData = data.stock;
            }

            this.$store.commit(
                "setCloneHomeIconDetailList",
                this.tableData
            );
            // const demoPrice = 0.654879;
            // const demoChange = 50;
            // const High = 0.654879;
            // const Low = 0.000079;
            // const Volume = 40938.4265;
            // this.tableData.forEach(v => {
            //     v.Price = (demoPrice + Math.random()).toFixed(6);
            //     v.Change = (demoChange + Math.random() * 30).toFixed(2);
            //     v.High = (High + Math.random()).toFixed(6);
            //     v.Low = (Low + Math.random()).toFixed(6);
            //     v.Volume = (Volume + Math.random() * 1000).toFixed(2);
            // });
        }
    },
};
</script>

