<template>
    <div id="app">
        <loadingOverlay v-if="isLoading"></loadingOverlay>
        <div v-bind:style="{visibility: !isHidden ? 'visible' : 'hidden'}">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import loadingOverlay from "@/components/loadingOverlay";
export default {
    name: "app",
    components: {
        loadingOverlay
    },
    data() {
        return {
            isHidden: true
        }
    },
    mounted() {
        setTimeout( () => {
            this.$store.commit(
                "setLoading",
                true
            )

            setTimeout(() => {
                this.isHidden = false;
                this.$store.commit(
                    "setLoading",
                    false
                )
                // this.$router.push({
                //     name: "homeSection"
                // });
            }, 1800);
        });
    },
    computed: {...mapState({
        isLoading: state => { return state.isLoading }
    })}
};
</script>

<style>
    #app {
        font-family: "Avenir", Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        width: 100%;
        margin: auto;
    }
</style>
