<template>
    <div id="Suspension" class="Suspension">
        <el-table :data="OrderData" style="width: 100%" :empty-text="' '" :height="domHeight">
            <el-table-column
                prop="marketCode"
                :label="$t('main.OrderRecord.Name')"
                align="center"
                width="100px"
            ></el-table-column>
            <el-table-column prop="tradeType" :label="$t('main.OrderRecord.Time')" align="center">
                <template slot-scope="scope">{{(scope.row.tradeType).split('_')[1]}}</template>
            </el-table-column>
            <el-table-column
                prop="Access"
                :label="$t('main.OrderRecord.Access')"
                align="center"
                width="150px"
            >
                <template slot-scope="scope">
                    <i class="el-icon-s-promotion pointer" style="color:#fff" @click="jumpRowChart(scope.row)"></i>
                </template>
            </el-table-column>
            <el-table-column prop="tradeId" :label="$t('main.OrderRecord.OrderID')" align="center">
            </el-table-column>
            <el-table-column
                prop="Position"
                :label="$t('main.OrderRecord.Position')"
                align="center"
            >
                <template slot-scope="scope">
                    <span v-if="(scope.row.tradeType).split('_')[3] == 'B'" style="color:#1DD086">{{$t('main.OrderRecord.up')}}</span>
                    <span v-else style="color:#E62250">{{$t('main.OrderRecord.down')}}</span>
                </template>
            </el-table-column>
            <el-table-column
                prop="createTime"
                :label="$t('main.OrderRecord.OriginTime')"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="marketPrice"
                :label="$t('main.OrderRecord.OriginPrice')"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="tradePriceYuan"
                :label="$t('main.OrderRecord.Amount')"
                align="center"
            ></el-table-column>
            <el-table-column :label="$t('main.OrderRecord.CurrentPrice')" align="center" v-if="type == 'OpenPosition'">
                <template slot-scope="scope">
                    <span :style="{color: (chartData.price >= scope.row.marketPrice) ? '#1DD086' : '#e62250'}">
                        {{scope.row.nowPrice}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column :label="$t('main.OrderRecord.endPrice')" align="center" v-if="type == 'history'">
                <template slot-scope="scope">
                    <span>
                        {{scope.row.remark}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column prop="settleRatio" :label="$t('main.OrderRecord.Odds')" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.settleRatio+'%'}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="Payouts" :label="$t('main.OrderRecord.Payouts')" align="center" v-if="type == 'history'" >
                <template slot-scope="scope">
                    <span>{{scope.row.settlePriceYuan}}</span>
                </template>
            </el-table-column>
            <el-table-column :label="$t('main.OrderRecord.RemainingTime')" align="center" v-if="type == 'OpenPosition'">
                <template slot-scope="scope">
                    <span>{{scope.row.countdown}}</span>
                </template>
            </el-table-column>
            <el-table-column align="center" v-if="type == 'OpenPosition'">
                <template slot-scope="scope">
                    <!-- 这里判断大于0 是因为切换周期的时候percentageNumber会变成负数 -->
                    <div v-if="scope.row.percentageNumber > 0">
                        <el-progress :percentage="scope.row.percentageNumber" :show-text="false"  style="padding-right:20px"></el-progress>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <placeholder v-if="!(OrderData && OrderData.length) && sceenWidth <= 768" :icon="'el-icon-s-data'" 
            :message="'main.OrderRecord.NoCurrentOrders'"></placeholder>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { Event } from '../utils/eventBus'
import placeholder from '@/components/placeholder'
export default {
    name: "Suspension",
    props: ['OrderData','type'],
    data() {
        return {
            domHeight: '150px',
            timer: null
        };
    },
    computed: {
        OrderDatas(){
            return this.OrderData;
        },
        SuspensionEndTime() {
            return this.$store.state.SuspensionEndTime;
        },
        ...mapState({
            chartData: state => state.chartData,
            minute :state => state.chartPeriod,
            sceenWidth: state => state.sceenWidth,
            chartPeriod: state => state.chartPeriod
        })

    },
    watch: {
        OrderData: function (data){
            this.resetCountdown(data)
        }
    },
    created() {
    },
    mounted() {
        this.$nextTick(() => {
            // 直接设置不生效，放到this.$nextTick里才会生效
            const domOffset = document.getElementById('Suspension').getBoundingClientRect()
            const sceenHeight = document.body.clientHeight
            const domHeight = sceenHeight - domOffset.top - 10
            // 设置最小高
            this.domHeight = domHeight < 150 ? '150px' : domHeight - 30 + 'px'
        })
        
    },
    beforeDestroy() {
        this.clearInterval()
    },
    methods: {
        jumpRowChart (row){
            let obj = {
                market: row.marketType,
                code: row.marketCode
            }
            // 保存此时的币种，同时emit触发
            this.$common.setCookie("CurrencyType", obj);
            this.$store.commit('setIconInfo', obj)

            // 查看是否处于当前周期，不处于的话，到父组件调用切换方法
            let period = row.tradeType.split('_')[1]
            if(period !== this.chartPeriod){
                this.$emit('switchChart')
            }else{
                Event.$emit('switchIcon')
            }
        },
        clearInterval (){
            if(this.timer){
                clearInterval(this.timer);
            }
        },
        resetCountdown (data){
            this.clearInterval()
            this.timer = setInterval(() => {
                data.forEach(v => {
                    let suspensionEndTime = v.endTimeStamp;  //终点时间
                    if(suspensionEndTime){
                        let timeStamp = suspensionEndTime - Date.parse(new Date())
                        if(timeStamp < 0){
                            v.countdown = "00：00：00";
                            v.percentageNumber = 0
                            return false
                        }
                        let date = new Date(timeStamp);
                        let m = date.getMinutes() ; //获取分钟数(0-59)
                        let s = date.getSeconds() ; //获取秒数(0-59)
                        v.minutes = m > 5 ? 0 : m;
                        v.seconds = s;
                        if (m < 10) {
                            m = "0" + m;
                        }
                        if (s < 10) {
                            s = "0" + s;
                        }
                        v.countdown = "00：" + m + "：" + s;

                        let minute = Number(v.tradeType.split('_')[1].split('')[0]);//当前所在分秒区
                        let total = (Number(v.minutes) * 60) + Number(v.seconds);//当前总数
                        let totalNumber = minute * 60;//总数
                        let remaining = totalNumber - total;
                        v.percentageNumber = remaining / totalNumber * 100;
                    }else{
                        v.countdown = "00：00：00";
                        v.percentageNumber = 0
                    }
                });
            }, 1000)
        }
    },
    components: {
        placeholder
    }
};
</script>

<style lang="scss">
.el-table{
    .el-table__empty-block {
        background: #202432;
    }
}
.Suspension {
    position: relative;
    /deep/ .el-table {
        table {
            width: 100% !important;
        }
        background: #202432;
        tr {
            background: #202432;
            height: 30px;
            td {
                padding: 3px;
            }
        }
        .cell {
            padding: 0;
            font-size: 11px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        ::-webkit-scrollbar-track-piece {
            //滚动条凹槽的颜色，还可以设置边框属性
            background-color: #202432;
        }
        ::-webkit-scrollbar {
            //滚动条的宽度
            width: 6px;
        }
        ::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background-color: #7d8da1;
            background-clip: padding-box;
        }
        ::-webkit-scrollbar-thumb:hover {
            background-color: #bbb;
        }
        .el-table__empty-block {
            background: #202432;
        }
    }
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background: #202432;
    }
    .el-table__body,
    .el-table__footer,
    .el-table__header {
        .has-gutter tr th {
            background: #202432;
            color: #dddee5;
            padding: 8px 0;
            border-bottom: 1px solid rgba(120, 123, 137, 1);
        }
    }
    .el-table::before {
        height: 0;
    }
    .el-table__body-wrapper tr td {
        background: #202432;
        border-bottom: none;
        color: #dddee5;
    }
    .hoverColor {
        font-size: 20px;
        line-height: 24px;
        &:hover {
            color: #ffc000;
        }
    }
}
</style>
