/**
 * axios封装
 * 请求拦截、响应拦截、错误统一处理
 */
import Vue from 'vue'
import axios from 'axios';
import router from '../router';
import store from '../store/index';
import qs from 'qs'; // 根据需求是否导入qs模块
import base from '@/api/base'; // 导入接口域名列表
import {
    Message
} from 'element-ui';

/**
 * 提示函数
 * 禁止点击蒙层、显示一秒后关闭
 */
const tip = msg => {
    Message({
        message: msg,
        type: 'warning'
    });
}

/**
 * 跳转登录页
 * 携带当前页面路由，以期在登录页面完成登录后返回当前页面
 */
const toLogin = () => {
    router.replace({
        path: '/login',
        query: {
            redirect: router.currentRoute.fullPath
        }
    });
}

/**
 * 请求失败后的错误统一处理
 * @param {Number} status 请求失败的状态码
 */
const errorHandle = (status, other) => {
    // 状态码判断
    switch (status) {
        // 401: 未登录状态，跳转登录页
        case 401:
            toLogin();
            break;
        // 403 token过期
        // 清除token并跳转登录页
        case 403:
            sessionStorage.removeItem('okbix-token')
            break;
        // 404请求不存在
        case 404:
            tip('请求的资源不存在');
            break;
        default:
            console.log(other);
    }
}

// 创建axios实例
var instance = axios.create({
    timeout: 1000 * 12
});
// 设置post请求头
instance.defaults.headers.post['Content-Type'] = 'application/json';
/**
 * 请求拦截器
 * 每次请求前，如果存在token则在请求头中携带token
 */
instance.interceptors.request.use(
    config => {
        // pc token应该加上Bearer前缀
        const prefixToken = 'Bearer '
        const token = sessionStorage.getItem('okbix-token')
        token && (config.headers.token = `${prefixToken}${token}`);
        return config;
    },
    error => Promise.error(error)
)

// 响应拦截器
instance.interceptors.response.use(
    // 请求成功
    res => {
        if(res.status === 200){
            if(res.data.code === 0){
                return Promise.resolve(res)
            }else{
                if(res.data.msg && res.data.msg.includes('token')){
                    tip('登录状态过期');
                    sessionStorage.removeItem('okbix-token')
                }else{
                    tip(res.data.msg)
                }
            }
        }else{
            return Promise.reject(res)
        }
    },
    // 请求失败
    error => {
        const {
            response
        } = error;
        if (response) {
            // 请求已发出，但是不在2xx的范围
            errorHandle(response.status, response.data.message);
            return Promise.reject(response);
        } else {
            console.log(error)
            // tip('请求超时')
        }
    }
);

const ajax = function (type, url, query){
    let option = {
        method: type,
        url: url,
        baseURL: base.okbix,
    }

    if(type.toUpperCase() === 'GET'){
        option.params = query
    }else if(type.toUpperCase() === 'POST'){
        option.data = query
    }
    return instance(option)
};

export default ajax