<!--
 * @Descripttion: 首页币种表格
 * @Author: wangxi
 * @Date: 2019-12-05 13:19:07
 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-17 23:13:12
 -->
<template>
    <div class="GeneralPurpose">
        <el-table ref="generalPurposeRef" class="mobile-hide" :data="tableData" style="width: 100%" :empty-text="' '">
            <el-table-column align="center" width="50px">
                <template slot-scope="scope">
                    <i
                        v-if="loginStatus"
                        class="el-icon-star-on pointer hoverColor"
                        :class="{'isCollection' : (scope.row.collec == 1)}"
                        @click="Favorites(scope.row)"
                    ></i>
                </template>
            </el-table-column>
            <el-table-column
                prop="code"
                :label="$t('main.HomeList.Name')"
                align="center"
                width="100px"
            >
                <template slot-scope="scope">
                    <span style="cursor: pointer;" @click="jumoTransactionPage">{{scope.row.code}}</span>
                </template>
            </el-table-column>
            <el-table-column :label="$t('main.HomeList.UpTrades')" align="center" width="200px">
                <template slot-scope="scope">
                    <span style="color:#1DD086">
                        <i class="el-icon-arrow-up"></i>
                        {{(chartData[scope.row.code].up)+'%'}}
                    </span>
                    <span style="margin:0 5px">/</span>
                    <span style="color:#E62250">
                        <i class="el-icon-arrow-down"></i>
                        {{(chartData[scope.row.code].down)+'%'}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column prop="nowPrice" :label="$t('main.HomeList.Price')" align="center"></el-table-column>
            <el-table-column prop="Change" :label="$t('main.HomeList.Change')" align="center">
                <template slot-scope="scope">
                    <span
                        v-if="scope.row.Change >= 0"
                        style="color:#1DD086;"
                    >{{'+'+scope.row.Change + '%'}}</span>
                    <span v-else style="color:#E62250;">{{scope.row.Change || 0 + '%'}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="High" :label="$t('main.HomeList.High')" align="center">
                <template slot-scope="scope">
                    <span style="color:#1DD086;">{{scope.row.High}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="Low" :label="$t('main.HomeList.Low')" align="center">
                <template slot-scope="scope">
                    <span style="color:#E62250;">{{scope.row.Low}}</span>
                </template>
            </el-table-column>
            <el-table-column
                prop="Volume"
                :label="$t('main.HomeList.Volume')"
                align="center"
                width="150px"
            >
                <template slot-scope="scope">
                    <span>{{scope.row.Volume}}</span>
                </template>
            </el-table-column>
            <el-table-column
                prop="Volume"
                :label="$t('main.HomeList.Access')"
                align="center"
                width="150px"
            >
                <template slot-scope="scope">
                    <i
                        class="el-icon-s-promotion pointer"
                        style="color:#fff"
                        @click="jumoTransactionPage(scope.row)"
                    ></i>
                </template>
            </el-table-column>
        </el-table>
        <table class="mobile-show mobile-table">
            <thead>
                <tr>
                    <th>{{$t('main.HomeList.Name')}}</th>
                    <th>{{$t('main.HomeList.UpTrades')}}</th>
                    <th>{{$t('main.HomeList.Price')}}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="row in tableData" :key="row.id">
                    <td>
                        <div class="first-row name">
                            <i
                                v-if="loginStatus"
                                class="el-icon-star-on pointer hoverColor-mobile"
                                :class="{'isCollection' : (row.collec == 1)}"
                                @click="Favorites(row)"
                            ></i>
                            {{row.code}}
                        </div>24h Change
                        <span class="up">(change value)</span>
                    </td>
                    <td>
                        <div class="first-row up-down">
                            <span class="up">
                                <i class="el-icon-arrow-up"></i>({{(chartData[row.code].up)+'%'}})
                            </span> /
                            <span class="down">
                                <i class="el-icon-arrow-down"></i>{{(chartData[row.code].down)+'%'}}
                            </span>
                        </div>
                    </td>
                    <td>
                        <div class="first-row price">{{row.nowPrice || 0}}</div>
                        <button class="trade-btn" @click="jumoTransactionPage(row)">Trade</button>
                    </td>
                </tr>
            </tbody>
        </table>
        <placeholder
            v-if="!(tableData && tableData.length)"
            :icon="'el-icon-document'"
            :message="'main.OrderRecord.NoTrades'"
            :buttonProps="{label: 'main.OrderRecord.StartTrading', path: 'transactionPage'}"
        ></placeholder>
    </div>
</template>

<script>
import { mapState } from "vuex";
import placeholder from "@/components/placeholder";
export default {
    name: "GeneralPurpose",
    props: ["tableData"],
    data() {
        return {
            loginStatus: false
        };
    },
    watch: {
        SuspensionEndTime: function() {
            this.getOddsTion();
        }
    },
    created() {
        this.loginStatus = this.$common.getCookie("token");
        this.getOddsTion();
    },
    mounted (){
    },
    methods: {
        getOddsTion() {
            this.$http.ProductList().then(res => {
                let dataMap = res.data.data;
                Object.keys(dataMap).forEach(key => {
                    this.$store.state[key] = dataMap[key];
                });
            });
        },
        jumoTransactionPage(row) {
            // 保存此时的币种，同时emit触发
            this.$common.setCookie("CurrencyType", row);
            this.$store.commit("setIconInfo", row);
            this.$router.push({
                name: "transactionPage"
            });
        },
        Favorites(msg) {
            let index = 0;
            if (msg.market == "1") {
                index = 2;
            } else if (msg.market == "fx") {
                index = 1;
            } else if (msg.market == "2") {
                index = 3;
            }
            let obj = {
                market: msg.market,
                code: msg.code,
                status: msg.collec
            };
            let message = this.$t('main.tip.Collectionsuccess');
            if (msg.collec == 1) {
                message = this.$t('main.tip.Cancelsuccess');
            }
            this.$http.Collection(obj).then(res => {
                if (res) {
                    this.$notify({
                        title: this.$t('main.tip.success'),
                        message: message,
                        type: "success"
                    });
                    this.$emit("getData", index);
                }
            });
        },
    },
    computed: {
        ...mapState({
            chartData: state => state,
            SuspensionEndTime: state => state.SuspensionEndTime
        })
    },
    components: {
        placeholder
    }
};
</script>

<style lang="scss">
.GeneralPurpose {
    .el-table tbody tr:hover > td {
        background-color: #202432 !important;
    }
    .el-table {
        background: #202432;
        font-size: 12px;
    }
    .el-table__body,
    .el-table__footer,
    .el-table__header {
        .has-gutter tr th {
            background: #191c25;
            color: #dddee5;
            padding: 2px 0;
            border-bottom: none;
        }
    }
    .el-table::before {
        height: 0;
    }
    .el-table__body-wrapper tr td {
        background: #202432;
        border-bottom: none;
        color: #dddee5;
    }
    .hoverColor {
        font-size: 20px;
        line-height: 24px;
        &:hover {
            color: #ffc000;
        }
    }
    .hoverColor-mobile{
        font-size: 20px;
        line-height: 24px;
    }
    .isCollection {
        color: #ffc000;
    }

    @media screen and (max-width: 768px) {
        .mobile-table {
            text-align: left;
            display: table;
            width: calc(100% - 20px);
            margin: auto;
            font-size: 12px;
            color: #787b89;
            thead {
                tr {
                    border-bottom: 1px solid #787b89;
                    th {
                        padding: 16px 0 12px 0px;
                    }
                }
            }
            tbody {
                tr {
                    border-bottom: 1px solid #787b89;
                    td {
                        padding: 10px 0 5px 0px;

                        .first-row {
                            height: 25px;

                            &.name {
                                font-size: 16px;
                                color: #dddee5;
                            }

                            &.price {
                                color: #dddee5;
                            }
                        }

                        .trade-btn {
                            background-color: #3f434e;
                            cursor: pointer;
                            color: #dddee5;
                            border: none;
                            padding: 1px 12px;
                            border-radius: 2px;
                            margin-top: -7px;
                        }
                    }
                }
            }
        }
    }
}
</style>
