<!--
 * @Descripttion: 交易界面
 * @Author: wangxi
 * @Date: 2019-11-30 18:57:24
 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-18 00:55:27
 -->
<template>
    <div class="transactionPage">
        <div class="mobile-show">
            <mobileHeader :returnPath="'/'" :showFavorite="true" :showHistory="true" :title="chartData.showCode"></mobileHeader>
        </div> 
        <div class="main-content">
            <div class="nameIndex">
                <span class="mobile-hide">{{chartData.showCode}}</span>
                <div class="current-price">
                    <div class="current-price-label mobile-hide">{{$t('main.nav.CurrentPrice')}}:</div>
                    <div class="current-price-amount">{{chartData.price}}</div>
                    <div class="hour-change mobile-show">
                        24{{$t('main.nav.HourChange')}}：
                        <span style="color: #1dd086;">--</span>
                    </div>
                </div>
                <div class="center_l_t">
                    <div class="tab_box">
                        <el-tabs
                            v-model="chartPeriodType"
                            type="card"
                            @tab-click="switchChartPeriod"
                            class="selectionPeriod"
                        >
                            <el-tab-pane :label="$t('main.nav.minute1')" name="one"></el-tab-pane>
                            <el-tab-pane :label="$t('main.nav.minute5')" name="five"></el-tab-pane>
                        </el-tabs>
                        <el-select
                            v-model="optionsValue"
                            @change="optionsChange"
                            class="DropdownSelectiontime"
                        >
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                        <div class="tab_box_r">
                            <p class="mobile-hide">
                                {{$t('main.nav.Odds')}}：
                                <span style="color: #1dd086;">
                                    <span class="direction-symbol">&#x25B2;</span>
                                    {{$store.state.riseOdds}}%
                                </span>/
                                <span style="color: #E62250;">
                                    <span class="direction-symbol">&#x25BC;</span>
                                    {{$store.state.fallOdds}}%
                                </span>
                            </p>
                            <p class="mobile-hide">
                                {{$t('main.nav.CycleChange')}}：
                                <span style="color: #1dd086;">--</span>
                            </p>
                        </div>
                    </div>
                </div>
                <div class="mobile-show volume-up-down">
                    <table>
                        <tr>
                            <td>{{$t('main.nav.Volume')}}</td>
                            <td class="volume">{{chartData.price}}</td>
                        </tr>
                        <tr>
                            <td>{{$t('main.nav.Up')}}</td>
                            <td class="up">{{$store.state.riseOdds}}%</td>
                        </tr>
                        <tr>
                            <td>{{$t('main.nav.Down')}}</td>
                            <td class="down">{{$store.state.fallOdds}}%</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="center_box">
                <div class="desktop-reverse">
                    <div class="center_l">
                        <div id="chart-container" class="chart-container">
                            <bussiness-chart
                                ref="tradeChart"
                                @endTrade="displayTradeResult"
                                :len="chartPeriodCount"
                            ></bussiness-chart>
                            <div
                                class="results-description mobile-hide"
                            >{{$t('main.chart.ResultHistory')}}</div>
                            <div class="results-history" id="results-history">
                                <div
                                    style="display: flex;transition: all 1s;"
                                    :style="{transform: `translateX(-${historyOffsetLeft}px)`}"
                                >
                                    <div class="fade-in mobile-show"></div>
                                    <div
                                        class="results-history-column"
                                        v-for="(item, index) in outcomeHistoryShowData"
                                        :key="index"
                                    >
                                        <img
                                            v-if="Number(item.status) === 0"
                                            src="../assets/success.svg"
                                            alt
                                        />
                                        <img v-else src="../assets/failure.svg" alt />
                                    </div>
                                    <div class="time-icon">
                                        <i class="el-icon-time"></i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="center_c">
                        <div class="pricePublic_box">
                            <div class="seamless-warp">
                                <div class="seamless_top">
                                    <span>{{$t('main.priceList.price')}}</span>
                                    <span>{{$t('main.priceList.Amount')}}</span>
                                    <span>{{$t('main.priceList.time')}}</span>
                                </div>
                                <div class="scroll_box">
                                    <ul
                                        class="item scroll_tion"
                                        :style="{top:ListTop,transition:transition}"
                                        v-if="priceList.length"
                                    >
                                        <li
                                            v-for="(item,index) in priceList"
                                            :key="index"
                                            :style="{color: Number(item.type) === 1 ? '#e62250' : '#1dd086' }"
                                        >
                                            <span class="title" v-text="item.price"></span>
                                            <span class="amount">{{item.coinCount / 100}}</span>
                                            <span class="time">{{$date.formaMinuteTime(item.time)}}</span>
                                        </li>
                                    </ul>
                                    <div v-else class="NoPriceList">{{$t('main.priceList.NoData')}}</div>
                                </div>
                            </div>
                            <div class="scale-thermometer">
                                <div
                                    class="scale-structure scale-up"
                                    :style="{height: upPercentage * 240 + 'px'}"
                                ></div>
                                <div
                                    class="scale-structure scale-down"
                                    :style="{height: downPercentage * 240 + 'px'}"
                                ></div>
                            </div>
                        </div>
                        <div class="Ranking_box">
                            <pricePublic type="rank" :dataList="rankingList" />
                        </div>
                    </div>
                </div>

                <div class="center_r">
                    <div class="business-collection">
                        <el-tabs v-model="iconType" @tab-click="switchIconData">
                            <el-tab-pane :label="$t('main.Currency.Collection')" name="collection"></el-tab-pane>
                            <el-tab-pane :label="$t('main.Currency.commodity')" name="product"></el-tab-pane>
                            <el-tab-pane :label="$t('main.Currency.index')" name="index"></el-tab-pane>
                            <el-tab-pane :label="$t('main.Currency.stockMarket')" name="stock"></el-tab-pane>
                        </el-tabs>
                        <pricePublic
                            type="icon"
                            :dataList="iconDetailList"
                            @collectionCallback="getAllIconData"
                        />
                    </div>
                    <Detriment class="Detriment_box" @getOrderData="getOrderData" />
                </div>
            </div>
            <div class="clearfix"></div>
            <footer class="order-info-history">
                <el-tabs v-model="activeName3" type="card" @tab-click="handleClick">
                    <el-tab-pane :label="$t('main.TransactionRecord.Position')" name="Suspension">
                        <Suspension
                            :OrderData="OrderData.holdingList"
                            type="OpenPosition"
                            v-if="activeName3 == 'Suspension'"
                            @switchChart="manualSwitchChartPeriod"
                        />
                    </el-tab-pane>
                    <el-tab-pane
                        :label="$t('main.TransactionRecord.historyRecord')"
                        name="historyRecord"
                    >
                        <Suspension
                            :OrderData="OrderData.historyList"
                            type="history"
                            v-if="activeName3 == 'historyRecord'"
                        />
                    </el-tab-pane>
                </el-tabs>
            </footer>
            <!-- 交易结果 -->
            <div
                v-if="tradeResultStatus"
                class="modals-container-default"
                @click="tradeResultStatus = false"
            ></div>
            <div class="modal-container-trade-history" :class="{show: tradeResultStatus}">
                <div v-if="tradeResultStatus && allIconCategoryData.length > 1" class="prev">
                    <i
                        class="el-icon-arrow-left Fluorescence"
                        :class="{'hide' : !switchChartResultStatus}"
                        @click="switchResultChart(-1)"
                    ></i>
                </div>
                <div style="width:100%">
                    <div v-if="tradeResultStatus" class="head">
                        <h1>{{$t('main.OrderRecord.result')}}</h1>
                        <el-radio-group
                            v-model="chartResultPeriod"
                            fill="#2294e6"
                            size="mini"
                            @change="displayCurrentPeriodChart(chartResultPeriod)"
                        >
                            <el-radio-button v-if="has1MDataStatus" label="1M">1M</el-radio-button>
                            <el-radio-button v-if="has5MDataStatus" label="5M">5M</el-radio-button>
                        </el-radio-group>
                        <i
                            class="close el-icon-close"
                            @click="tradeResultStatus = false;switchChartResultStatus = true"
                        ></i>
                    </div>
                    <div v-if="tradeResultStatus" class="panes">
                        <div class="panes-holder">
                            <div class="panes-scroller">
                                <div class="history-chart" style="width: 100%;">
                                    <div v-if="switchChartResultStatus" class="canvas_box">
                                        <!-- canvas截图 -->
                                        <!-- <img :src="htmlUrl" style="width: 100%;" /> -->
                                        <!-- echarts绘图 -->
                                        <bussinessChartPopUps ref="bussinessChartPopUps" />
                                    </div>
                                    <div class="view" v-else>
                                        <div class="deals-table">
                                            <div class="vertical-scroll" style="height: 100%;">
                                                <div class="inner-scroll-container">
                                                    <div>
                                                        <div
                                                            class="row"
                                                            v-for="(item, index) in lastMinuteList"
                                                            :key="index"
                                                        >
                                                            <div class="first">
                                                                <div class="major">
                                                                    {{item.tradeTime.split(' ')[1]}}
                                                                    <span
                                                                        v-if="(item.tradeType).split('_')[3] == 'B'"
                                                                        style="color:#1DD086"
                                                                    >{{$t('main.OrderRecord.up')}}</span>
                                                                    <span
                                                                        v-else
                                                                        style="color:#E62250"
                                                                    >{{$t('main.OrderRecord.down')}}</span>
                                                                </div>
                                                                <div class="minor">
                                                                    {{item.tradeTime.split(' ')[0]}}
                                                                    <span>{{item.marketCode}}</span>
                                                                </div>
                                                            </div>
                                                            <div class="second">
                                                                <div
                                                                    class="major levels"
                                                                    :style="{color: (item.marketPrice < item.remark) ? '#1DD086' : '#e62250'}"
                                                                >{{$t('main.OrderRecord.SettlementPrice')}}：{{item.remark}}</div>
                                                                <div
                                                                    class="minor levels level-down"
                                                                >{{$t('main.OrderRecord.finalPrice')}}：{{item.marketPrice}}</div>
                                                            </div>
                                                            <div class="third">
                                                                <div class="major result green">
                                                                    <span
                                                                        class="money undefined money-medium"
                                                                        :style="{color: (item.tradeType).split('_')[3] == 'B' ? '#1DD086' : '#E62250'}"
                                                                    >{{item.settlePriceYuan}}</span>
                                                                </div>
                                                                <div class="minor">
                                                                    <span
                                                                        class="money undefined money-medium"
                                                                    >{{item.tradePriceYuan}}</span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="info">
                                        <div class="result-bar">
                                            <div class="item profit">
                                                <div class="title">{{$t('main.OrderRecord.profit')}}</div>
                                                <div class="value positive">
                                                    <span
                                                        class="money undefined money-medium"
                                                    >{{totalLastMinuteAmount.toFixed(2)}}</span>
                                                </div>
                                            </div>
                                            <div class="side-button" @click="switchChartResultFun">
                                                <i
                                                    class="Fluorescence"
                                                    :class="{'el-icon-s-unfold' : switchChartResultStatus,'el-icon-s-fold':!switchChartResultStatus}"
                                                ></i>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="tradeResultStatus && allIconCategoryData.length > 1" class="next">
                    <i
                        :class="{'hide' : !switchChartResultStatus}"
                        class="el-icon-arrow-right Fluorescence"
                        @click="switchResultChart(1)"
                    ></i>
                </div>
            </div>
            <!-- 交易结果 -->
        </div>
    </div>
</template>

<script>
import axios from "axios";
import mobileHeader from '@/components/mobileHeader';
import BussinessChart from "@/components/bussinessChart";
import bussinessChartPopUps from "@/components/bussinessChartPopUps";
import pricePublic from "@/components/pricePublic";
import Suspension from "@/components/suspension";
import Detriment from "@/components/detriment";
import { mapState } from "vuex";
import SockJS from "sockjs-client";
import Stomp from "stompjs";
let time;
export default {
    name: "transactionPage",
    data() {
        return {
            options: [
                {
                    value: "one",
                    label: this.$t("main.nav.minute1")
                },
                {
                    value: "five",
                    label: this.$t("main.nav.minute5")
                }
            ],
            optionsValue: this.$t("main.nav.minute1"),
            // 图表结果截图展示flag
            switchChartResultStatus: true,
            chartPeriodType: "one",
            iconType: "collection",
            activeName3: "Suspension",
            // 排行列表
            rankingList: [],
            // 价格列表
            priceList: [],
            transformIconAjaxData: {
                collection: [], //收藏
                product: [], //商品
                index: [], //指数
                stock: [] //股市
            },
            upPercentage: 0.8,
            downPercentage: 0.2,
            // 图表历史数据
            outcomeHistoryShowData: [],
            OrderData: [],
            // 交易结果蒙版
            tradeResultStatus: false,
            // 图表期数偏移量
            historyOffsetLeft: 0,
            // 图表期数图标的宽
            iconWidth: 22,
            // 屏幕宽
            sceenWidth: 0,
            // websocket连接
            stompClient: null,
            // 结果展示的图表index
            chartResultIndex: 0,
            chartResultData: [],
            currentIconChartName: null,
            // 全部图表数据
            allChartResultData: [],
            allIconCategoryData: [],
            // 周期数目
            chartPeriodCount: 60,
            // 结算结果周期
            chartResultPeriod: "1M",
            // 是否有5m的数据
            has5MDataStatus: false,
            has1MDataStatus: false
        };
    },
    watch: {
        listData: function() {
            setTimeout(() => {
                this.getRankData();
            }, 2000);
        },
        currentIconInfo: function() {
            this.priceList = [];
        }
    },
    computed: {
        ...mapState({
            // 币种列表
            iconDetailList: state => state.iconDetailList,
            lastMinuteList: state => state.lastMinuteList,
            chartData: state => state.chartData,
            currentIconInfo: state => state.currentIconInfo,
            allIconNowPrice: state => state.allIconNowPrice,
            chartPeriod: state => state.chartPeriod
        }),
        htmlUrl() {
            return this.$store.state.htmlUrl;
        },
        ListTop() {
            let lengths = this.priceList;
            let x = 11;
            let y = lengths.length - x;
            let top = -(y * 25) + 25;
            if (lengths.length >= x) {
                return top + "px";
            } else {
                return "25px";
            }
        },
        transition() {
            let lengths = this.priceList;
            if (lengths.length == 20) {
                this.priceList.splice(0, 10);
                return "all 0s";
            } else {
                return "all .5s";
            }
        },
        listData() {
            return this.$store.state.SuspensionEndTime;
        },
        totalLastMinuteAmount() {
            return this.lastMinuteList.reduce((cur, next) => {
                return Number(cur) + Number(next.settlePriceYuan);
            }, 0);
        }
    },
    components: {
        BussinessChart,
        pricePublic,
        Suspension,
        Detriment,
        bussinessChartPopUps,
        mobileHeader
    },
    created() {
        this.$store.commit(
            "setChartPeriod",
            this.chartPeriodType === "one" ? "1M" : "5M"
        );
        let iconType = this.$common.getCookie("iconType");
        if (iconType) {
            this.iconType = iconType;
        }
        // 初始化所有的请求
        this.init();
        // 每隔两秒获取左上角实时订单
        time = setInterval(() => {
            this.getPriceData();
        }, 2000);
        // this.initTradeResultChart()
    },
    mounted() {
        const updateElementSize = event => {
            if (!event || event.target.outerWidth < 901) {
                const results = document.getElementById("results-history");
                if (results) results.scrollLeft = 100000;
            }
        };
        window.onresize = event => {
            updateElementSize();
            this.setHistoryOffset();
        };
        window.onload = () => {
            updateElementSize();
        };
        this.sceenWidth = document.body.clientWidth;
        this.$store.commit("setSceenWidth", this.sceenWidth);
        // this.$refs.bussinessChartPopUps.resetChart(this.chartResultData, this.currentIconChartName, this.chartResultPeriod)
    },
    methods: {
        optionsChange() {
            this.chartPeriodType = this.optionsValue;
            this.switchChartPeriod();
        },
        // 交易结果的多图表
        initTradeResultChart() {
            let allChartResultData = this.$common.getCookie(
                `${this.chartResultPeriod}-allChartResultData`
            );
            if (allChartResultData) {
                this.allChartResultData = JSON.parse(allChartResultData);
                this.allIconCategoryData = Object.keys(this.allChartResultData);
                this.currentIconChartName = this.allIconCategoryData[
                    this.chartResultIndex
                ];
                this.chartResultData = this.allChartResultData[
                    this.currentIconChartName
                ];
            }
        },
        // 切换图表和表格
        switchChartResultFun() {
            this.switchChartResultStatus = !this.switchChartResultStatus;
            this.$nextTick(() => {
                if (this.$refs.bussinessChartPopUps) {
                    this.$refs.bussinessChartPopUps.resetChart(
                        this.chartResultData,
                        this.currentIconChartName,
                        this.chartResultPeriod
                    );
                }
            });
        },
        // 多图表间切换
        switchResultChart(count) {
            let bussinessChartPopUps = document.getElementById(
                "bussinessChartPopUps"
            );
            let logo_box = document.getElementsByClassName("logo_box")[0];
            let endBorders = document.getElementsByClassName("endBorders")[0];
            hide(logo_box, 0.1);
            hide(endBorders, 0.1);
            hide(bussinessChartPopUps, 0.1);
            function hide(el, offset) {
                var opacity = el.style.opacity || 1;
                setTimeout(function() {
                    el.style.opacity = String(parseFloat(opacity) - offset);
                    parseFloat(el.style.opacity) > 0 && hide(el, offset);
                    if (el.style.opacity <= 0) {
                        show(el, 0.05);
                    }
                }, 17);
            }
            function show(el, offset) {
                var opacity = el.style.opacity || 0;
                setTimeout(function() {
                    el.style.opacity = String(parseFloat(opacity) + offset);
                    parseFloat(el.style.opacity) < 1 && show(el, offset);
                }, 17);
            }

            let allChartResultData = this.allChartResultData;
            this.chartResultIndex += count;
            if (this.chartResultIndex === -1) {
                this.chartResultIndex = this.allIconCategoryData.length - 1;
            } else if (
                this.chartResultIndex === this.allIconCategoryData.length
            ) {
                this.chartResultIndex = 0;
            }
            this.currentIconChartName = this.allIconCategoryData[
                this.chartResultIndex
            ];
            this.chartResultData =
                allChartResultData[this.currentIconChartName];
            this.$refs.bussinessChartPopUps.resetChart(
                this.chartResultData,
                this.currentIconChartName,
                this.chartResultPeriod
            );
        },
        manualSwitchChartPeriod() {
            this.chartPeriodType =
                this.chartPeriodType === "one" ? "five" : "one";
            this.switchChartPeriod();
        },
        // 切换图表分钟周期
        switchChartPeriod() {
            const s = new Date().getSeconds();
            // 图表最后结算阶段切换图表会有点问题，所以这里不准切换，是hack手段，后面需要修改一下判断方式
            if (s === 59 || s === 0 || s === 1) {
                this.chartPeriodType =
                    this.chartPeriodType === "one" ? "five" : "one";
                return false;
            }
            this.$store.commit(
                "setChartPeriod",
                this.chartPeriodType === "one" ? "1M" : "5M"
            );
            this.chartPeriodCount = this.chartPeriodType === "one" ? 60 : 300;
            // loading
            const loading = this.$loading({
                lock: true,
                text: this.$t("main.tip.switchingChartPeriod"),
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)"
            });

            setTimeout(() => {
                loading.close();
            }, 800);
            this.$refs.tradeChart.reloadChart();
        },
        // 展示交易结果弹窗
        displayTradeResult(timeStamp) {
            // 延迟两秒是因为后台结算会有延迟
            setTimeout(async () => {
                // 每次list为空，防止登录失效后不调用订单列表，list一直存在长度导致弹窗不弹出
                this.$store.commit("setLastMinuteList", []);
                await this.getOrderData();
                if (this.lastMinuteList.length > 0) {
                    this.getResultChartAjax();
                } else {
                    this.tradeResultStatus = false;
                }
            }, 2000);

            // 每次交易结束的时候请求一个期数
            let params = {
                code: this.chartData.code,
                market: this.chartData.market,
                prd: "1",
                count: 1
            };

            let time;
            if (this.outcomeHistoryShowData.length) {
                time = this.outcomeHistoryShowData[
                    this.outcomeHistoryShowData.length - 1
                ].time;
                params.time = time;
            }

            setTimeout(() => {
                this.getOutcomHistotyAjax(params).then(res => {
                    if (res) {
                        this.outcomeHistoryShowData = [
                            ...this.outcomeHistoryShowData,
                            ...res.data.data
                        ];
                        this.setHistoryOffset();
                    }
                });
            }, 1000);
        },
        // 返回结算单里这个周期的code集合
        returnCodeSet(period) {
            const lastMinuteList = this.lastMinuteList.filter(v =>
                v.tradeType.includes(period)
            );
            let codeSet = lastMinuteList.map(
                v => `${v.marketType}-${v.marketCode}`
            );
            codeSet = Array.from(new Set(codeSet));
            return codeSet;
        },
        // 返回结算单里这个周期的ajax集合
        returnAllBeforePriceAJaxArr(period) {
            let codeSet = this.returnCodeSet(period);
            let arr = [];
            codeSet.forEach(async v => {
                let codeArr = v.split("-");
                let query = {
                    market: codeArr[0],
                    code: codeArr[1],
                    id: 1,
                    prd: period
                };
                arr.push(this.$http.getBeforeEnterData(query));
            });

            return arr;
        },
        // 获取所有结算数据
        getResultChartAjax() {
            const oneM = "1M";
            const fiveM = "5M";
            this.$common.setCookie("allChartResultData", "");
            let ajax1M = this.returnAllBeforePriceAJaxArr(oneM);
            let ajax5M = this.returnAllBeforePriceAJaxArr(fiveM);

            let codeSet1M = this.returnCodeSet(oneM);
            let codeSet5M = this.returnCodeSet(fiveM);

            this.has1MDataStatus = false;
            this.has5MDataStatus = false;
            if (ajax1M.length) {
                this.has1MDataStatus = true;
                axios.all(ajax1M).then(
                    axios.spread((...rest) => {
                        let chartResultData = {};
                        rest.forEach((v, i) => {
                            let data = v.data.data;
                            chartResultData[codeSet1M[i]] = data.map(el => [
                                el.createdTime.replace(/-/g, "/"),
                                el.price
                            ]);
                        });
                        this.$common.setCookie(
                            `${oneM}-allChartResultData`,
                            JSON.stringify(chartResultData)
                        );
                        this.displayCurrentPeriodChart(oneM);
                    })
                );
            }

            if (ajax5M.length) {
                this.has5MDataStatus = true;
                axios.all(ajax5M).then(
                    axios.spread((...rest) => {
                        let chartResultData = {};
                        rest.forEach((v, i) => {
                            let data = v.data.data;
                            chartResultData[codeSet5M[i]] = data.map(el => [
                                el.createdTime.replace(/-/g, "/"),
                                el.price
                            ]);
                        });
                        this.$common.setCookie(
                            `${fiveM}-allChartResultData`,
                            JSON.stringify(chartResultData)
                        );
                        // 如果1m没有数据，5m返回里才调用
                        if (!ajax1M.length) {
                            this.chartResultPeriod = fiveM;
                            this.displayCurrentPeriodChart(fiveM);
                        }
                    })
                );
            }
        },
        // 切换展示图表结果周期数据
        displayCurrentPeriodChart(period) {
            // 展示出来
            this.tradeResultStatus = true;
            this.switchChartResultStatus = true;
            // 每次切换重置index
            this.chartResultIndex = 0;
            // 展示多图表
            let allChartResultData = this.$common.getCookie(
                `${period}-allChartResultData`
            );
            if (allChartResultData !== "{}") {
                this.allChartResultData = JSON.parse(allChartResultData);
                this.allIconCategoryData = Object.keys(this.allChartResultData);
                this.currentIconChartName = this.allIconCategoryData[
                    this.chartResultIndex
                ];
                this.chartResultData = this.allChartResultData[
                    this.currentIconChartName
                ];
                this.$nextTick(() => {
                    this.$refs.bussinessChartPopUps.resetChart(
                        this.chartResultData,
                        this.currentIconChartName,
                        this.chartResultPeriod
                    );
                });
            }
        },
        // 设置图表偏移
        setHistoryOffset() {
            // 每次根据屏幕设置不同的图表宽度
            this.sceenWidth = document.body.clientWidth;
            // 把当前屏幕存起来，可以用于其他界面根据宽度判断
            this.$store.commit("setSceenWidth", this.sceenWidth);
            // 手机的图标宽度为19，pc的宽度为22
            if (this.sceenWidth <= 768) {
                this.iconWidth = 19;
            } else {
                this.iconWidth = 22;
            }
            const historyDom = document.getElementById("results-history");
            const chartContainerDom = document.getElementById(
                "chart-container"
            );
            if(!chartContainerDom) {
                return;
            }

            // 获取期数外层dom宽，每次根据外层百分之九十的宽去计算多少个
            let chartContainerDomWidth = chartContainerDom.clientWidth;
            const totalCount = Math.round(
                (chartContainerDomWidth * 0.9) / this.iconWidth
            );
            // 将宽度设置成整数
            historyDom.style.width = this.iconWidth * totalCount + "px";
            // 超出宽度个数时，设置偏移
            // 这里totalCount + 1是因为有个时钟的小图标要展示出来
            if (this.outcomeHistoryShowData.length > totalCount) {
                if (this.sceenWidth <= 768) {
                    this.historyOffsetLeft =
                        (this.outcomeHistoryShowData.length - totalCount + 1) *
                        this.iconWidth;
                } else {
                    this.historyOffsetLeft =
                        (this.outcomeHistoryShowData.length - totalCount + 1) *
                        this.iconWidth;
                }
            } else {
                this.historyOffsetLeft = 0;
            }
        },
        // 获取持仓订单数据
        getOrderData() {
            let token = this.$common.getCookie("token");
            if (!token) {
                return false;
            }
            return new Promise((resolve, reject) => {
                this.$http.getOrderData().then(res => {
                    if (res) {
                        let arr = res.data.data;
                        // 持仓数据存在并且没有websocket连接，就重新连接，获取实时价格
                        if (arr.holdingList.length && !this.stompClient) {
                            this.initSocketJs();
                        }

                        // 将开始时间与结束时间丢进去
                        let allOrderTimeStampData = this.$common.getCookie(
                            "allOrderTimeStampData"
                        );
                        if (
                            allOrderTimeStampData &&
                            allOrderTimeStampData !== "{}"
                        ) {
                            let holdingList = [...arr.holdingList];
                            holdingList.forEach(v => {
                                if (
                                    JSON.parse(allOrderTimeStampData)[v.tradeId]
                                ) {
                                    v.initTimeStamp = JSON.parse(
                                        allOrderTimeStampData
                                    )[v.tradeId].initTimeStamp;
                                    v.endTimeStamp = JSON.parse(
                                        allOrderTimeStampData
                                    )[v.tradeId].endTimeStamp;
                                }
                            });
                            arr.holdingList = holdingList;
                        }

                        // 后端没按最新下单时间返回，前端排序一下
                        arr.holdingList.reverse();
                        arr.historyList.reverse();
                        this.OrderData = arr;
                        // 把lastMinuteList存起来，用于图表结果展示
                        // 只取已结算的数据  1未结算 2不中奖 3中奖
                        let lastMinuteList = arr.lastMinuteList.filter(
                            v => Number(v.status) !== 1
                        );
                        this.$store.commit("setLastMinuteList", lastMinuteList);
                        this.$store.commit("setHoldingList", arr.holdingList);
                        resolve(arr);
                    }
                });
            });
        },
        initSocketJs() {
            this.connection();
        },
        connection() {
            // 建立连接对象
            let socket = new SockJS(this.$base.websocketUri);
            // 获取STOMP子协议的客户端对象
            this.stompClient = Stomp.over(socket);
            this.stompClient.debug = null;
            this.stompClient.connect(
                {},
                data => {
                    this.stompClient.subscribe("/pic/all", response => {
                        this.$store.commit(
                            "setAllIconNowPrice",
                            JSON.parse(response.body)
                        );
                        this.OrderData.holdingList.forEach(v => {
                            if (this.allIconNowPrice) {
                                v.nowPrice = this.allIconNowPrice[v.marketCode];
                            }
                        });
                    });
                },
                err => {
                    // 连接发生错误时的处理函数
                    console.log("失败");
                    console.log(err);
                }
            );
        },
        disconnect() {
            if (this.stompClient) {
                this.stompClient.disconnect();
            }
        },
        init() {
            // 获取币种数据
            this.getAllIconData();
            // 获取价格模块数据
            this.getPriceData();
            // 获取排行模块数据
            this.getRankData();
            // 获取图表期数数据
            this.getOutcomHistoty();
            //获取购买订单数据
            this.getOrderData();
        },
        getPriceData() {
            let obj = {
                market: this.currentIconInfo.market,
                code: this.currentIconInfo.code,
                type: this.chartPeriod
            };
            this.$http.getTradeVolume("", obj).then(res => {
                if (res) {
                    let x =
                        res.data.data.proportion == 0
                            ? 50
                            : res.data.data.proportion;
                    this.upPercentage = Number(x) / 100;
                    this.downPercentage = (100 - Number(x)) / 100;
                    this.priceList = [
                        ...this.priceList,
                        ...res.data.data.trade
                    ];
                }
            });
        },
        getRankData() {
            this.$http.getTradeRank().then(res => {
                if (res) {
                    this.rankingList = res.data.data;
                    this.rankingList.sort((a, b) => {
                        return b["profit"] - a["profit"];
                    });
                }
            });
        },
        // 获取所有币种信息，根据market分成三个数组放置
        getAllIconData() {
            this.$http.currencyForm().then(res => {
                if (res) {
                    res.data.data.forEach(v => {
                        v.showCode = v.code
                            .replace(/^USD/, "$&/")
                            .replace(/USD$/, "/$&");
                        v.collectionStatus = 0;
                    });
                    let result = res.data.data;
                    this.transformIconAjaxData.product = result.filter(
                        v => v.market === "fx"
                    );
                    this.transformIconAjaxData.index = result.filter(
                        v => v.market === "1"
                    );
                    this.transformIconAjaxData.stock = result.filter(
                        v => v.market === "2"
                    );

                    this.getCollectionData(result);
                } else {
                }
            });
        },
        // 获取初始的图表期数
        async getOutcomHistoty() {
            // 防止图表此时尚没有数据
            if (!this.chartData.code && !this.chartData.market) {
                setTimeout(() => {
                    this.getOutcomHistoty();
                }, 1000);
                return false;
            }
            const params = {
                code: this.chartData.code,
                market: this.chartData.market,
                prd: "1"
            };
            this.getOutcomHistotyAjax(params).then(res => {
                if (res) {
                    this.outcomeHistoryShowData = res.data.data;
                    this.setHistoryOffset();
                }
            });
        },
        // 获取图表期数ajax
        getOutcomHistotyAjax(params) {
            return this.$http.getPreviousData(params);
        },
        // 切换货币类型，每次展示当前market的货币数据
        switchIconData() {
            this.$store.dispatch(
                "setIconDetailList",
                this.transformIconAjaxData[this.iconType]
            );
            this.$store.dispatch(
                "setCloneIconDetailList",
                this.transformIconAjaxData[this.iconType]
            );
            this.$common.setCookie("iconType", this.iconType);
        },
        // 获取收藏币种
        getCollectionData(allIconData) {
            let token = this.$common.getCookie("token");
            if (!token) {
                this.switchIconData();
                return false;
            }
            this.$http.userDetails().then(res => {
                if (res) {
                    // 把个人信息里的收藏币种code拿出来，循环所有币种，符合的添加标记
                    let collectionList = res.data.data.favorite;
                    let codes = collectionList.map(v => v.code);
                    allIconData.forEach(item => {
                        if (codes.length && codes.includes(item.code)) {
                            item.collectionStatus = 1;
                        }
                    });
                    this.transformIconAjaxData.collection = allIconData.filter(
                        v => v.collectionStatus === 1
                    );
                    this.switchIconData();
                }
            });
        },
        handleClick() {}
    },
    destroyed() {
        this.disconnect();
        clearInterval(time);
    }
};
</script>

