<template>
    <div id="header">
        <div class="header mobile_header_box">
            <div class="return">
                <img v-if="returnPath" v-on:click="routerPath()" src="../assets/back-icon.svg" alt />
            </div>
            <div class="flex-spacer"></div>
            <div>{{title}}</div>
            <div class="flex-spacer"></div>
            <div class="star">
                <!-- <img src="../../assets/favorite.svg" alt=""> -->
                <i  v-if="showFavorite"
                    class="el-icon-star-on pointer hoverColor"
                    :class="{'isCollection' : (LightUp == 1)}"
                    @click="Favorites"
                    style="font-size:32px"
                ></i>
                <i v-if="showHistory" class="icon-clock" style="margin-left: 10px;" @click="routerPath('/trade-history')"></i>
            </div>
        </div>
        <div class="mobile-header-placeholder"></div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import mobileHeader from '@/components/mobileHeader';

export default {
    name: "mobileHeader",
    data() {
        return {
            LightUp: 0,
            dataList: []
        }
    },
    props: {
        title: {
            type: String,
            default: ''
        },
        showFavorite: {
            type: Boolean,
            default: false
        },
        showHistory: {
            type: Boolean,
            default: false
        },
        returnPath: {
            type: String,
            default: ''
        }
    },
    components: {
        mobileHeader
    },
    computed: {
        ...mapState({
            chartData: state => state.chartData,
        }),
        transactionPageHeader() {
            if (this.$route.name == "transactionPage") {
                return true;
            }
        },
        loginStatus() {
            return this.$common.getCookie("token");
        }
    },
    created() {
        
    },
    watch: {
        chartData: function() {
            if (this.getNum) {
                this.getOddsTion();
            }
            this.getNum = false;
        }
    },
    methods: {
        routerPath(path) {
            if(path) {
                this.$router.push(path)
                return;
            }
            
            if(this.returnPath) {
                this.$router.push(this.returnPath);
            }
        },
        getData() {
            this.LightUp = 0;
            this.$http.currencyForm().then(res => {
                let result = res.data.data;
                this.dataList = result;
                result.forEach(msg => {
                    if (!msg.collec) {
                        msg.collec = 0;
                    }
                });
                let collecList = [];
                let token = this.$common.getCookie('token')
                if(!token){
                    return false
                }
                
                this.$http.userDetails().then(res => {
                    if (res) {
                        collecList = res.data.data.favorite;
                        this.dataList.forEach(item => {
                            collecList.forEach(items => {
                                if (item.code == items.code) {
                                    item.collec = items.status;
                                }
                            });
                        });
                        this.collection = this.dataList.filter(
                            v => v.collec == 1
                        );
                        if (this.collection.length > 1) {
                            this.collection.forEach(item => {
                                if (item.code == this.chartData.code) {
                                    this.LightUp = 1;
                                }
                            });
                        }
                    }
                });
            });
        },
        Favorites() {
            let msg = this.chartData;
            let obj = {
                market: msg.market,
                code: msg.code,
                status: this.LightUp
            };
            let message = this.$t("main.tip.Collectionsuccess");
            if (this.LightUp == 1) {
                message = this.$t("main.tip.Cancelsuccess");
            }
            this.$http.Collection(obj).then(res => {
                if (res) {
                    this.$notify({
                        title: this.$t("main.tip.success"),
                        message: message,
                        type: "success"
                    });
                    this.getData()
                }
            });
        }
    },
    beforeDestroy (){
    }
};
</script>

<style lang="scss">
.home {
    .header {
        width: 100%;
        background-color: #191c25;
        font-family: "Roboto", sans-serif;
        line-height: 45px;
        color: #dddee5;
    }
    .mobile-header-placeholder {
        width: 100vw;
        height: 45px;
    }
    .mobile_header_box {
        z-index: 10;
        position: fixed;
        padding-right: 10px;
        width: calc(100vw);
        display: flex;
        height: 45px;
        font-size: 25px;
        line-height: 45px;
        cursor: default;

        .return {
            cursor: pointer;
            min-width: 60px;
            img {
                margin: 10px;
                height: 25px;
            }
        }
        .star {
            cursor: pointer;
            margin-right: 10px;
            text-align: right;
            min-width: 60px;
            img{
                margin: 10px 3px 10px 10px;
                height: 25px;
            }    
            i {
                vertical-align: top;
                position: relative;
                top: 8px;

                &.icon-clock {
                    top: 12px;
                }
            }
        }
        .flex-spacer {
            flex-grow: 1;
        }
    }
}
</style>
