<template>
    <div id="register">
        <div class="login-registration-header">
            <div @click="returnHome">
                <i class="el-icon-close"></i>
            </div>
            <div @click="switchLang" class="lang">{{$t('main.nav.LanguageaSwitching')}}</div>
        </div>
        <el-form class="loginFrom" :model="registerdata" ref="ruleForm">
            <el-form-item class="login-item">
                <img class="logo" src="../../assets/okbix-logo-vertical.png" alt />
            </el-form-item>
            <el-form-item class="login-item responsive-input">
                <el-select
                    v-model="currentAccountType"
                    :placeholder="$t('main.LoginRegistration.SelectAccountType')"
                >
                    <el-option
                        v-for="item in accountType"
                        :key="item.value"
                        :label="$t(item.label)"
                        :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="currentAccountType === 1" prop="email" class="form-group responsive-input">
                <div class="mobile-show label">{{$t('main.LoginRegistration.Email')}}</div>
                <el-input
                    class="login-inputorbuttom"
                    prefix-icon="el-icon-user"
                    :placeholder="$t('main.LoginRegistration.Email')"
                    v-model.trim="registerdata.email"
                    @blur="registerCheck('email')"
                ></el-input>
            </el-form-item>
            <el-form-item prop="username" class="form-group responsive-input">
                <div class="mobile-show label">{{$t('main.LoginRegistration.Username')}}</div>
                <el-input
                    class="login-inputorbuttom"
                    prefix-icon="el-icon-user"
                    :placeholder="$t('main.LoginRegistration.Username')"
                    v-model.trim="registerdata.username"
                    @blur="registerCheck('username')"
                ></el-input>
            </el-form-item>
            <el-form-item prop="password" class="form-group responsive-input" style="position: relative;">
                <div class="mobile-show label">{{$t('main.LoginRegistration.Password')}}</div>
                <el-input
                    :type="passwordType"
                    class="login-inputorbuttom"
                    prefix-icon="el-icon-lock"
                    :placeholder="$t('main.LoginRegistration.Password')"
                    v-model.trim="registerdata.password"
                ></el-input>
                <span v-if="registerdata.password" class="password-icon" @click="switchPasswordType">
                    <i v-if="passwordType === 'password'" class="icon-visibility_off"></i>
                    <i v-else class="icon-remove_red_eye"></i>
                </span>
            </el-form-item>
            <el-form-item class="form-group checkbox-styled">
                <input type="checkbox" v-model="isAgreeStatus"/>
                <span class="checkmark"></span>
                <div class="label">{{$t('main.LoginRegistration.TermsOfService')}}</div>
            </el-form-item>
            <!-- <el-form-item prop="password class="form-group">
                <div class="mobile-show label">再次输入密码</div>
                <el-input
                    type="password"
                    class="login-inputorbuttom"
                    prefix-icon="el-icon-lock"
                    placeholder="再次输入密码"
                    v-model.trim="registerdata.passwordAgain"
                ></el-input>
            </el-form-item>-->
            <el-form-item class="login-item">
                <vaptcha @success="validSuccess"></vaptcha>
            </el-form-item>
            <el-form-item class="login-item">
                <el-button
                    class="login-inputorbuttom register"
                    type="primary"
                    @click="submitRegister"
                >{{$t('main.LoginRegistration.Register')}}</el-button>
            </el-form-item>
            <el-form-item class="login-item">
                <el-button
                    class="login-inputorbuttom login-bottom"
                    type="primary"
                    @click="jumpLogin"
                >{{$t('main.LoginRegistration.Login')}}</el-button>
            </el-form-item>
            <el-form-item class="login-item">
                <el-button
                    class="login-inputorbuttom login-bottom"
                    @click="goToDemo()"
                >{{$t('main.LoginRegistration.DemoAccess')}}</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import vaptcha from "@/components/vaptcha.vue";
export default {
    name: "register",
    data() {
        return {
            registerdata: {
                email: "",
                password: "",
                // passwordAgain: "",
                username: "",
                ip: null
            },
            validStatus: false,
            currentAccountType: 1,
            accountType: [
                {
                    value: 0,
                    label: "main.LoginRegistration.DemoAccount"
                },
                {
                    value: 1,
                    label: "main.LoginRegistration.OfficialAccount"
                }
            ],
            isAgreeStatus: false,
            timer: null,
            passwordType: 'password'
        };
    },
    components: {
        vaptcha
    },
    computed: {
        captchaStatus() {
            return captchaStatus;
        },
        popUpMode() {
            return popUpMode;
        }
    },
    created (){
        var script = document.createElement("script");
        script.src = "http://pv.sohu.com/cityjson?ie=utf-8";
        script.defer = "defer"
        document
            .getElementsByTagName("head")[0]
            .appendChild(script);
    },
    mounted() {
        this.timer = setInterval(() => {
            if(window.returnCitySN && window.returnCitySN["cip"]){
                clearInterval(this.timer)
                this.registerdata.ip = window.returnCitySN["cip"]
            }
        }, 1000)
    },
    destroyed() {
        // 离开页面时隐藏掉弹窗
        const popEl = document.getElementsByClassName("vaptcha-popup");
        document.getElementsByClassName("vaptcha-popup")[
            popEl.length - 1
        ].style.display = "none";
        // 清除定时器
        clearInterval(this.timer)
    },
    methods: {
        switchPasswordType (){
            this.passwordType = this.passwordType === 'password' ? 'text' : 'password'
        },
        goToDemo() {
            this.$router.push({
                name: "login",
                query: {
                    isDemo: 'true'
                }
            });
        },
        jumpLogin() {
            this.$router.push({
                name: "login"
            });
        },
        // 人机验证成功
        validSuccess(token) {
            if (token) {
                this.validStatus = true;
            }
        },
        switchLang() {
            this.$i18n.locale = this.$i18n.locale == "zh" ? "en" : "zh";
        },
        returnHome() {
            this.$router.push({
                name: "homeSection"
            });
        },
        // 验证是否存在
        registerCheck(type) {
            let validFlag =
                type === "email"
                    ? this.commonEmailValid()
                    : this.commonUsernameValid();
            if (!validFlag) {
                return false;
            }
            let params = {};
            params[type] = this.registerdata[type];
            this.$http.registerCheck(params).then(res => {
                if (res) {
                }
            });
        },
        // 共用邮箱验证
        commonEmailValid() {
            if (!this.registerdata.email) {
                this.$message(this.$t('main.tip.cannotbeempty'));
                return false;
            }

            const emailRegx = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
            if (!emailRegx.test(this.registerdata.email)) {
                this.$message(this.$t('main.tip.correctemailaddress'));
                return false;
            }

            return true;
        },
        // 共用用户名验证
        commonUsernameValid() {
            if (!this.registerdata.username) {
                this.$message(this.$t('main.tip.Usernamecannotbeempty'));
                return false;
            }

            if (this.registerdata.username.length <= 2) {
                this.$message(this.$t('main.tip.greaterthan2digits'));
                return false;
            }

            return true;
        },
        submitRegister() {
            if(!this.registerdata.ip){
                this.$$message(this.$t('main.tip.laterAgain'))
                return false
            }

            // 正式账号才验证邮箱
            if (this.currentAccountType === 1) {
                if (!this.commonEmailValid()) {
                    return false;
                }
            }

            if (!this.commonUsernameValid()) {
                return false;
            }

            if (!this.registerdata.password) {
                this.$message(this.$t('main.tip.passwordcannotbeblank'));
                return false;
            }

            // 最短6位，最长16位
            // 必须包含1个数字
            // 必须包含1个小写字母
            // 必须包含1个大写字母
            // 必须包含1个特殊字符
            const passwordRegx = /^.*(?=.{6,16})(?=.*\d)(?=.*[A-Z]{1,})(?=.*[a-z]{1,})(?=.*[!@#$%^&*?\(\)\.]).*$/;
            if (!passwordRegx.test(this.registerdata.password)) {
                this.$message(this.$t('main.tip.numbersandspecialcharacters'));
                return false;
            }

            if (!this.validStatus) {
                this.$message(this.$t('main.tip.Pleaseverifyfirst'));
                return false;
            }

            if(!this.isAgreeStatus){
                this.$message(this.$t('main.tip.agreeService'));
                return false
            }

            if (this.currentAccountType === 1) {
                this.$http.register(this.registerdata).then(res => {
                    if (res) {
                        this.afterLoginHandle();
                    }
                });
            } else {
                const demoParams = Object.assign({}, this.registerdata);
                delete demoParams.email;
                this.$http.demoRegister(demoParams).then(res => {
                    if (res) {
                        this.afterLoginHandle();
                    }
                });
            }
        },
        afterLoginHandle() {
            this.$message(this.$t('main.tip.registrationsuccess'));
            setTimeout(() => {
                this.$router.push({
                    name: "login",
                    query: {
                        username: this.registerdata.username
                    }
                });
            }, 300);
        }
    }
};
</script>

<style lang="scss">
.password-icon{
    position: absolute;
    right: 15px;
    bottom: 0;
    color: #C0C4CC;
}
#register {
    padding-top: 20px;
    text-align: center;
    margin: auto;
    max-width: 500px;
    width: calc(100vw - 50px);
    .login-item {
        margin-bottom: 5px;
    }

    .login-registration-header {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        i {
            cursor: pointer;
            color: #787b89;
            font-size: 26px;
        }
        .lang {
            color: #04affe;
            cursor: pointer;
            line-height: 27px;
        }
    }

    #vaptchaContainer {
        display: inline-block;
        margin-top: 25px;
    }

    .el-form {
        width: 100%;

        .logo {
            width: 160px;
        }

        button.el-button {
            background-color: transparent;
            border: none;
            color: #04affe;
        }
        button.el-button.register {
            background: linear-gradient(to left, #06a1fd, #7e10bf);
            width: 80%;
            border: none;
            color: #dddee5;
            border-radius: 1px;
        }
    }

    @media screen and (max-width: 768px) {
        .el-form {
            .form-group {
                color: #dddee5;
                text-align: left;
                .label {
                    margin-left: 2px;
                    height: 31px;
                }
            }
            .el-input__prefix {
                display: none;
            }

            button.el-button.register {
                background: none;
                background-color: transparent;
                width: 100%;
                border: 1px solid #04affe;
                color: #04affe;
            }
        }
    }
}
</style>