<template>
    <div class="Tutorial">
        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="80%"
            :before-close="handleClose"
        >
            <el-carousel
                :interval="5000"
                arrow="never"
                trigger="click"
                :autoplay="false"
                ref="carousel"
                :loop="false"
            >
                <el-carousel-item v-for="item in ImgLeght" :key="item">
                    <img :src="require(`@/assets/${imagesName}/nex_${item}.png`)" alt />
                </el-carousel-item>
            </el-carousel>
            <span slot="footer" class="dialog-footer">
                <el-button
                    type="success"
                    @click="handleClose()"
                    v-if="isShow"
                >{{$t('main.guideMap.skip')}}</el-button>
                <el-button @click="prev()">{{$t('main.guideMap.prev')}}</el-button>
                <el-button type="primary" @click="next()">{{endText}}</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "Tutorial",
    props: {},
    data() {
        return {
            imagesName: "zhImages",
            ImgLeght: 11,
            dialogVisible: false,
            isShow: true,
            endText: this.$t("main.guideMap.next")
        };
    },
    computed: {},
    created() {
        let dialogVisible = this.$common.getCookie("dialogVisible");
        let langToken = this.$common.getCookie("local-lang");
        if (langToken == "en") {
            this.imagesName = "enImages";
        }
        if (dialogVisible) {
            this.dialogVisible = true;
        }
    },
    mounted() {},
    watch: {},
    methods: {
        prev() {
            this.endText = this.$t("main.guideMap.next");
            this.isShow = true;
            this.$refs.carousel.prev();
        },
        next() {
            if (!this.isShow) {
                this.dialogVisible = false;
                this.$emit('shutDown')
                this.$common.removeCookie("dialogVisible");
                return;
            }
            this.$refs.carousel.next();
            if (this.$refs.carousel.activeIndex == this.ImgLeght - 1) {
                this.endText = this.$t("main.guideMap.done");
                this.isShow = false;
            }
        },
        handleClose() {
            this.dialogVisible = false;
            this.$common.removeCookie('dialogVisible');
            this.$emit('shutDown');
        }
    },
    components: {}
};
</script>

<style  lang="scss">
.Tutorial {
    .el-dialog {
        height: 70%;
        .el-dialog__header {
            display: none;
        }
        .el-dialog__body {
            padding: 0;
            height: 100%;
            .el-carousel__container,
            .el-carousel--horizontal {
                height: 100%;
            }
            .el-carousel__item.is-animating {
                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .el-dialog__footer {
            padding: 10px 20px 10px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            background: #202432;
        }
    }
}

@media screen and (max-width: 1280px) {
    .Tutorial .el-dialog {
        height: 60%;
    }
}
@media screen and (max-width: 1024px) {
    .Tutorial .el-dialog {
        width: 100% !important;
        height: 50%;
    }
}
@media screen and (max-width: 768px) {
    .Tutorial .el-dialog {
        height: 35%;
        margin-top: 25vh !important;
        .el-carousel__indicators {
            display: none;
        }
        .el-dialog__footer{
            padding: 10px;
            .dialog-footer{
                .el-button{
                    padding: 5px 20px;
                }
            }
        }
    }
}
</style>
