<template>
    <div class="nav-sidebar">
        <div class="menu">
            <div class="sidebar-title">
                {{$t(sidebarTitle)}}
            </div>
            <div v-bind:class="{ active: item.translation == selectedItem }" v-for="(item, i) in menuItems" :key="i" class="gradient-wrapper">
                <div class="menu-item" @click="item.name === 'logout' ? logout() : routerPath(item.name)">{{$t(item.translation)}}</div>
            </div>
        </div>
        <div v-if="!hideDepositBtn" class="deposit-btn">
            <button @click="routerPath('deposit')">
                {{$t('main.navSidebar.Deposit')}}
            </button>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        menuItems: {
            type: Array,
            default: []
        },
        selectedItem: {
            type: String,
            default: ''
        },
        sidebarTitle: {
            type: String,
            default: ''
        },
        hideDepositBtn: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {};
    },
    name: "navSidebar",
    computed: {
    },
    created() {
    },
    mounted() {
    },
    methods: {
        routerPath(name) {
            if(name) {
                this.$router.push({
                    name: name
                });
            }
        },
        logout() {
            alert('Implement logout');
        }
    },
    beforeDestroy (){
    }
};
</script>
<style lang="scss" scoped>
    .nav-sidebar {
        width: 100%;
        font-size: 12px;
        .menu {
            border: 1px solid #787B89;
            width: 100%;
            margin-bottom: 17px;
            .gradient-wrapper {
                &.active {
                    padding-right: 3px;
                    background: linear-gradient(to bottom, #06a1fd, #7e10bf);
                    .menu-item {
                        background-color: #303542;
                    }
                }
            }
            .sidebar-title {
                text-align: center;
                padding: 14px 20px;
            }
            .menu-item {
                color: #dddee5;
                padding: 8px 20px;
                cursor: pointer;
                &:hover {
                    background-color: #303542;
                }
            }
        }
        .deposit-btn {
            text-align: center;
            width: 100%;
            margin: auto;

            button {
                cursor: pointer;
                padding: 8px;
                width: 90%;
                color: #dddee5;
                border: none;
                background: linear-gradient(to left, #06a1fd, #7e10bf);
                &:active {
                    opacity: .8;
                }
            }
        }
    }
</style>