<template>
    <div>
        <div class="mobile-show">
            <mobileHeader :returnPath="'/assets'" :title="$t('main.navSidebar.History')"></mobileHeader>
        </div> 
        <div class='asset-account-container history-page'>
            <div class="sidebar">
                <navSidebar :menuItems="menuList" :sidebarTitle="sidebarTitle" :selectedItem="selectedItem"></navSidebar>
            </div>
            <div class="main-content">
                <div class="heading">{{$t('main.historyPage.AssetHistory')}}</div>
                <div class="table-container">
                    
                    <div class="header-tabs responsive-tabs no-bottom-border with-divider">
                        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                            <el-tab-pane :label="$t('main.HomeList.Collection')" name="Deposits"></el-tab-pane>
                            <el-tab-pane :label="$t('main.HomeList.Commodities')" name="Withdrawals"></el-tab-pane>
                        </el-tabs>
                    </div>
                    
                    <div class="mobile-hide">
                        <table>
                            <thead>
                                <tr>
                                    <th>{{$t('main.historyPage.Asset')}}</th>
                                    <th>{{$t('main.historyPage.Amount')}}</th>
                                    <th>{{$t('main.historyPage.Date')}}</th>
                                    <th>{{$t('main.historyPage.Status')}}</th>
                                    <th colspan="2">{{$t('main.historyPage.Address')}}</th>
                                </tr>
                            </thead>
                            <tbody>
                                <template v-for="(item, i) in history">
                                    <tr :key="i" class="no-border">
                                        <td>{{item.currencyType}}</td>
                                        <td>{{item.amount}}</td>
                                        <td>{{item.date}}</td>
                                        <td>{{item.status}}</td>
                                        <td>{{item.address}}</td>
                                        <td class="txid-icon">
                                            <i @click="showItemTxid(item)" v-if="!item.showTxid" class="el-icon-arrow-down"></i>
                                        </td>
                                    </tr>
                                    <tr :key="i + 'a'" v-bind:class="{ noPadding: !item.showTxid }">
                                        <td colspan="2"></td>
                                        <td class="txid" colspan="3">
                                            <span v-if="item.showTxid">{{item.txid}}</span>
                                        </td>
                                        <td class="txid-icon" >
                                            <i v-if="item.showTxid" @click="showItemTxid(item)" class="el-icon-arrow-up"></i>
                                        </td>
                                    </tr>
                                </template>
                            </tbody>
                            
                        </table>
                    </div>
                    <div class="mobile-show">
                        <table>
                            <tr v-for="(item, i) in history" :key="i">
                                <td>
                                    <span class="currency">{{item.currencyType}}</span><span>{{item.amount}}</span>
                                    <div class="time">{{item.date}}</div>
                                </td>
                                <td><div class="status">{{item.status}}</div></td>
                            </tr>                        
                        </table>
                    </div>
                    
                    <placeholder v-if="!(history && history.length)" :icon="'el-icon-document'" 
                        :message="'main.historyPage.NoDeposits'" 
                        :buttonProps="{label: 'main.historyPage.MakeDeposit', path: 'deposit'}"></placeholder>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
import navSidebar from "@/components/navSidebar";
import { NavigationItems } from '@/common/navigation-items.js'
import placeholder from '@/components/placeholder'
import mobileHeader from '@/components/mobileHeader'
export default {
    name:'historyPage',
    props: {

    },
    data() {
        return {
            sidebarTitle: NavigationItems.accountAssetsTitle,
            menuList: NavigationItems.accountAssets,
            selectedItem: 'main.navSidebar.History',
            activeName: '',
            history: [
                // {
                //     currencyType: 'USDT',
                //     date: '2019-01-23 14:12:43',
                //     amount: 14376.12,
                //     status: 'Complete',
                //     address: 'OX347ifjsdfie0934DSFIJieJSAI93403',
                //     txid: 'uduSPF8udfu0sDf234S4DgE6Fdsqwej9d90sfJ9fd9fs0f9e039jpoks8w9'
                // }
            ]
        };
    },
    computed: {

    },
    created() {

    },
    mounted() {

    },
    watch: {

    },
    methods: {
        handleClick(e) {
            let index = e.index;
        },
        showItemTxid(item) {
            item.showTxid = !item.showTxid;
            this.$forceUpdate(); 
        }
    },
     components: {
        navSidebar,
        mobileHeader,
        placeholder
    },
};
</script>

<style scoped lang="scss">
    .history-page {
        .table-container {
            padding: 10px;
            border: 1px solid #787b89;
            padding-bottom: 40px;
        }
        table {
            margin-top: 10px;
            font-size: 12px;
            width: 100%;
            td {
                padding: 10px 0;
                &.txid-icon {
                    width: 40px;
                    i {
                        cursor: pointer;
                    }
                }
                &.txid {
                    text-align: right;
                    padding-right: 10px;
                }
            }
            tr {
                border-bottom: 1px solid #787b89;
                &.no-border {
                    border-bottom: none;
                }

                &.noPadding {
                    td {
                        padding: 0;
                    }
                }
            }
            thead {
                tr {
                    th {
                        padding: 7px;
                        color: #787b89;
                        background-color: #303542;
                    }
                }
            }
        }

        @media screen and (max-width: 768px) {
            .table-container {
                border: none;
                padding: 0;
            }
            table {
                font-size: 14px;
                width: 90%; 
                margin: 10px auto;
                td {
                    color: #dddee5;
                    
                    .status {
                        color: #787b89;
                        text-align: right;
                        position: relative;
                        top: 5px;
                    }
                    .currency {
                        display: inline-block;
                        width: 60px;
                    }
                    .time {
                        padding-top: 5px;
                        font-size: 11px;
                        color: #787b89;
                    }
                    
                }
            }
            &.asset-account-container {
                padding-top: 0 !important;
            }
            .header-tabs {
                padding-top: 10px;
                background-color: #191c25;
            }
        }
    }
</style>
