<style lang="scss">
    .loading-overlay {
        background-color: #191c25;
        z-index: 25;
        width: 100vw;
        height: 100vh;
        position: fixed;
        text-align: center;
        left: 0;
        top: 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        .loading-container {
            width: calc(100% - 50px);
            max-width: 700px;
            margin: auto;
        }

        @keyframes in-place {
            to {
                transform: none;
            }
        }

        #icon {
            transform: scale(0) rotate(-180deg);
            animation: in-place .7s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
        }

        #letter-1,
        #letter-2,
        #letter-3,
        #letter-4,
        #letter-5 {
            transform: scale(0);
            animation: in-place .5s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
        }

        #letter-1 {
            animation-delay: .4s;
        }
        #letter-2 {
            animation-delay: .45s;
        }
        #letter-3 {
            animation-delay: .5s;
        }
        #letter-4 {
            animation-delay: .55s;
        }
        #letter-5 {
            animation-delay: .6s;
        }
    }
    
    
</style>
<script>
import { mapState } from "vuex";
export default {
    data() {
        return {};
    },
    name: "loadingOverlay",
    computed: {...mapState({
        isLoading: state => {}
    })},
    created() {
    },
    mounted() {
        const elems = document.querySelectorAll(".loading-overlay #icon, .loading-overlay #letter-1, .loading-overlay #letter-2, .loading-overlay #letter-3, .loading-overlay #letter-4, .loading-overlay #letter-5");
        elems.forEach(function(el) {
            let bbox = el.getBBox();
            let center = {
                x: bbox.x + bbox.width/2, 
                y: bbox.y + bbox.height/2
            };
            el.style.transformOrigin = center.x + "px " + center.y + "px";
        });
    },
    methods: {
    },
    beforeDestroy (){
    }
};
</script>
