<template>
    <div class="placeholder" v-bind:style="{'margin-top': topMargin ? topMargin : '45px' }">
        <div class="icon">
            <i v-if="icon" v-bind:class="icon"></i>
        </div>
        <!-- <pre class="message">{{$t(message)}}</pre> -->
        <pre class="message">{{$t("main.OrderRecord.nodata")}}</pre>
        <div class="button-container" v-if="buttonProps">
            <el-button @click="routerPath(buttonProps.path)" class="responsive-button gradient">{{$t(buttonProps.label)}}</el-button>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        message: {
            type: String,
            default: ''
        },
        icon: {
            type: String,
            default: ''
        },
        buttonProps: {
            type: Object,
            default: null
        },
        topMargin: {
            type: String,
            default: ''
        }
    },
    data() {
        return {};
    },
    name: "placeholder",
    computed: {
    },
    created() {
    },
    mounted() {
    },
    methods: {
        routerPath(name) {
            if(name) {
                this.$router.push({
                    name: name
                });
            }
        }
    },
    beforeDestroy (){
    }
};
</script>
<style lang="scss" scoped>
    .placeholder {
        font-size: 12px;
        width: 100%;
        text-align: center;
        color: #787B89;
        padding-bottom: 50px;
        .icon {
            height: 100px;
            margin-bottom: 10px;
            font-size: 90px;
        }
        .message {
             margin-bottom: 40px;
        }
        .button-container {
            text-align: center;   
            .el-button {
                max-width: 375px;
            }
        }

        @media screen and (max-width: 768px) {
            font-size: 13px;
            .icon {
                height: 40px;
                margin-bottom: 10px;
                font-size: 40px;
            }
            .message {
                margin-bottom: 20px;
            }
            .button-container {
                text-align: center;   
                .el-button {
                    max-width: 375px;
                }
            }
            .button-container {
                padding: 0 10%;
                .el-button {
                    max-width: 100%;
                }
            }
        }
    }
    
</style>