<template>
    <div class="Detriment">
        <div class="position-header">{{$t('main.BuyList.MyPosition')}}</div>
        <div class="amount-input-container">
            <el-input
                @blur="isInputSelected=false"
                v-bind:class="{ 'input-selected': isInputSelected }"
                class="amount-input"
                ref="myInput"
                type="number"
                v-model="businessCount"
                @input="handleChangeCount"
                :autofocus="false"
                :disabled="isDisableStatus"
                :placeholder="$t('main.BuyList.NumberOfTransactions')"
            ></el-input>
            <div class="top_text">
                {{userInfo.status === 1 ? `(${$t('main.BuyList.ImpersonateUser')})` : ''}}
                {{$t('main.BuyList.AvailableFunds')}}：
                <span>{{realIcon || 0}}</span>
            </div>
        </div>

        <div class="UpsAndDowns flex_c_a">
            <div class="up-container">
                <el-button class="hvr-bounce-to-right" type="success" :disabled="canBuyStatus" @click="submitBusiness(1)">
                    <p>{{$t('main.BuyList.UP')}}</p>
                </el-button>
            </div>
            <div class="down-container">
                <el-button class="hvr-bounce-to-left" type="danger" :disabled="canBuyStatus" @click="submitBusiness(-1)">
                    <p>{{$t('main.BuyList.DOWN')}}</p>
                </el-button>
            </div>
        </div>

        <div class="bet-container">
            <div class="multiple_box discrete">
                <div class="btn_box" v-for="item in businessPercentList" :key="item.id">
                    <el-button class="percent btn-jelly" @click="chooseBusinessPercent(item.params)">{{item.value}}</el-button>
                </div>
                <div class="btn_box" v-for="(item,index) in multipleList" :key="index">
                    <el-button class="btn-jelly" @click="chooseBusinessCount(item)">{{item}}</el-button>
                </div>
            </div>
            <el-button class="double-button btn-jelly" @click="doubleBusiness">{{$t('main.BuyList.Double')}}</el-button>
        </div>

        <div class="UpsAndDowns flex_c_a">
            <div class="up-container">
                <div v-if="riseOdds">{{riseOdds}}%</div>
                <div v-else>{{$t('main.priceList.NoData')}}</div>
            </div>
            <div class="down-container">
                <div v-if="fallOdds">{{fallOdds}}%</div>
                <div v-else>{{$t('main.priceList.NoData')}}</div>
            </div>
        </div>
        <div class="est-text">
            <!-- {{$t('main.BuyList.EstPay')}} -->
        </div>
        <div class="UpsAndDowns flex_c_a">
            <div class="up">{{riseOddsAmount}}</div>
            <div class="down">{{fallOddsAmount}}</div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { Event } from '../utils/eventBus'
export default {
    name: "Detriment",
    props: {},
    data() {
        return {
            businessCount: null,
            currentBusinessCount: null,
            percentage: "0",
            canBuyStatus: false,
            beginSubmitBusiness: function() {},
            businessPercentList: [
                {
                    id: '1b',
                    value: '5%',
                    params: .05
                },
                {
                    id: '2b',
                    value: '10%',
                    params: .1
                },
                {
                    id: '3b',
                    value: '15%',
                    params: .15
                },
                {
                    id: '4b',
                    value: '20%',
                    params: .2
                },
            ],
            multipleList: [10, 50, 100, 500],
            isInputSelected: false,
            isDisableStatus: true
        };
    },
    computed: {
        ...mapState({
            assets: state => state.userInfo.coinAnalog,
            chartData: state => state.chartData,
            userInfo: state => state.userInfo,
            riseOdds: state => state.riseOdds,
            fallOdds: state => state.fallOdds,
            chartPeriod: state => state.chartPeriod,
            forbidChartStatus: state => state.forbidChartStatus
        }),
        realIcon() {
            return this.assets / 100;
        },
        riseOddsAmount() {
            if (!this.riseOdds) {
                return 0;
            }
            return (this.businessCount * this.riseOdds) / 100;
        },
        fallOddsAmount() {
            if (!this.fallOdds) {
                return 0;
            }
            return (this.businessCount * this.fallOdds) / 100;
        }
    },
    created() {
        // 购买按钮防抖
        this.beginSubmitBusiness = this.$common.debounce(this.submitBusiness);
    },
    mounted() {
        // 防止手机input默认聚焦
        setTimeout(() => {
            this.isDisableStatus = false
        }, 100)
        this.$refs.myInput.$refs.input.focus();
    },
    watch: {},
    methods: {
        // 思路：缓存一个初始值，百分比根据初始值进行改变，其他加倍等操作后重新设置初始值
        handleChangeCount(e) {
            this.clearPercentageFocus();
            const isIntger = this.$validate.validateInteger(e);
            if (!isIntger) {
                this.$message(this.$t("main.tip.canbeentered"));
                this.currentBusinessCount = Math.floor(e);
            } else {
                this.currentBusinessCount = e; //缓存一个初始交易值
            }
            this.validateBuyStatus();
        },
        switchPercentage() {
            this.businessCount = (this.realIcon * this.percentage) / 100;
            this.validateBuyStatus();
        },
        doubleBusiness() {
            this.businessCount = this.businessCount * 2;
            this.currentBusinessCount = this.businessCount;
            this.clearPercentageFocus();
            this.validateBuyStatus();
        },
        chooseBusinessPercent(percent) {
            this.businessCount = parseInt(this.realIcon * percent);
            this.validateBuyStatus();
        },
        chooseBusinessCount(count) {
            this.businessCount = count;
            this.currentBusinessCount = count;
            this.clearPercentageFocus();
            this.validateBuyStatus();
        },
        clearPercentageFocus() {
            this.percentage = "0";
        },
        validateBuyStatus() {
            const isOver = Number(this.businessCount) > Number(this.realIcon);
            this.canBuyStatus = isOver ? true : false;
        },
        submitBusiness(type) {
            //1涨 -1跌
            let token = this.$common.getCookie("token");
            if (!token) {
                this.$notify({
                    title: this.$t('main.tip.warning'),
                    message: this.$t('main.tip.beforeProceeding'),
                    type: "warning"
                });
                return false;
            }

            if (!this.riseOdds) {
                this.$message(this.$t("main.tip.unabletopurchase"));
                return false;
            }
            if (!this.businessCount || this.businessCount <= 0) {
                this.$message(this.$t("main.tip.transactionsfirst"));
                return false;
            }

            if (!this.chartData.market && !this.chartData.code) {
                this.$message(this.$t("main.tip.pleasewait"));
                return false;
            }
            
            if(this.canBuyStatus){
                this.$message({
                    type: 'warning',
                    message: this.$t("main.tip.InsufficientAccountBalance")
                });
                return false
            }

            // if(this.forbidChartStatus){
            //     this.$message({
            //         type: 'warning',
            //         message: this.$t("main.tip.Ordersended") 
            //     });
            //     return false
            // }

            const params = { 
                tradeType: `KL_${this.chartPeriod}_BS_${type === 1 ? "B" : "S"}`,
                tradePrice: this.businessCount,
                marketType: this.chartData.market,
                marketCode: this.chartData.code
            };
            let obj = Object.assign({}, this.chartData);
            obj.buyPrice = this.businessCount;
            obj.buyStatus = type;
            obj.chartPeriod = this.chartPeriod

            this.$http.trade(params).then(res => {
                if (res) {
                    const isZh = this.$i18n.locale === "zh";
                    this.$message(
                        `${
                            type === 1
                                ? this.$t("main.OrderRecord.up")
                                : this.$t("main.OrderRecord.down")
                        }  ${this.businessCount}${
                            isZh ? this.$t("main.tip.success") : ""
                        }`
                    );
                    // 注释代码为通知图表更新下单标记
                    Event.$emit('repaintOrderMark')
                    this.$store.commit("setCurrentBuyInfo", obj);
                    this.$emit("getOrderData");
                    
                    let allOrderTimeStampData = this.$common.getCookie('allOrderTimeStampData')
                    if(!allOrderTimeStampData){
                        allOrderTimeStampData = {}
                    }else{
                        allOrderTimeStampData = JSON.parse(allOrderTimeStampData)
                    }
                    
                    let orderId = res.data.data.tradeId
                    allOrderTimeStampData[orderId] = {
                        initTimeStamp: this.chartData.initTimeStamp,
                        endTimeStamp: this.chartData.endTimeStamp
                    }
                    
                    this.$common.setCookie('allOrderTimeStampData', JSON.stringify(allOrderTimeStampData))
                }else{
                    // this.$store.commit('removeCurrentBuyInfo')
                }
            }).catch((error) => {
                // this.$store.commit('removeCurrentBuyInfo')
            })
        }
    },
    components: {}
};
</script>

<style lang="scss">
.Detriment {
    font-size: 12px;
    color: #787b89;

    .position-header {
        background: #191c25;
        text-align: center;
        padding: 5px 5px;
    }

    .amount-input-container {
        padding: 10px;
        .input-selected {
            border: 1px solid #04affe;
        }
        .amount-input {
            input {
                border: none;
                border-radius: 0;
                border-bottom: 1px solid #787b89;
                background-color: transparent;
                height: 27px;
                line-height: 27px;
                padding: 0 5px;
                font-size: 12px;
                color: #dddee5;
                caret-color: #04affe;

                &::-webkit-input-placeholder {
                    /* Chrome/Opera/Safari */
                    color: #485371;
                }
                &::-moz-placeholder {
                    /* Firefox 19+ */
                    color: #485371;
                }
                &:-ms-input-placeholder {
                    /* IE 10+ */
                    color: #485371;
                }
                &:-moz-placeholder {
                    /* Firefox 18- */
                    color: #485371;
                }
            }
            // 去掉input右边箭头
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
                -webkit-appearance: none;
            }
            input[type="number"] {
                -moz-appearance: textfield;
            }
        }
    }

    .top_text {
        text-align: right;
        padding: 5px 0;
        font-size: 11px;
        color: #04affe;
    }

    .el-tabs {
        padding: 0 20px;
        .el-tabs__nav-scroll {
            display: flex;
            justify-content: center;
            .el-tabs__item {
                color: #787b89;

                &.is-active {
                    color: #04affe;
                }
            }
        }
        .el-tabs__header {
            margin: 0;
            .el-tabs__active-bar.is-top {
                display: none !important;
            }
            .el-tabs__nav-wrap::after {
                display: none !important;
            }
        }
    }

    .bet-container {
        padding: 0 10px 10px 10px;
        text-align: center;
        .el-button {
            color: #787b89;
            border-color: #787b89;
            background-color: transparent;
            height: 20px;
            padding: 0;
            margin: 0;

            &:focus {
                color: #04affe;
                border-color: #04affe;
            }
        }

        .multiple_box {
            display: flex;
            flex-wrap: wrap;
            &.discrete {
                height: 48px;
            }

            .btn_box {
                flex: 0 0 25%;
                width: 100%;
                display: flex;

                .el-button {
                    justify-content: space-between;
                    width: 100%;
                    margin: 0 2px;
                }
            }
        }

        .double-button {
            width: calc(100% - 4px);
        }
    }

    .UpsAndDowns {
        margin-bottom: 5px;
        .el-button {
            margin-top: 5px;
            padding: 11px 0;
            background: transparent;
            font-size: 15px;
            font-weight: bold;
            width: 85%;
        }

        .up-container {
            width: 50%;
            text-align: center;
            color: #1dd086;

            .el-button {
                color: #1dd086;
                border-color: #1dd086;
                margin-bottom: 4px;
            }

            .el-button--success.is-disabled:hover {
                background-color: transparent;
            }
        }

        .down-container {
            width: 50%;
            text-align: center;
            color: #e62250;

            .el-button {
                color: #e62250;
                border-color: #e62250;
                margin-bottom: 4px;
            }
            .el-button--danger.is-disabled:hover {
                background-color: transparent;
            }
        }
    }
    .est-text {
        text-align: center;
    }
    .ups-and-down-est {
    }

    .btn_text {
        text-align: center;
        line-height: 36px;
        background: #dcdfe6;
        border-radius: 4px;
    }

    @media screen and (max-width: 900px) {
        .amount-input-container {
            padding: 10px 10px 5px !important;
            .top_text {
                padding-bottom: 0px !important;
                margin-right: 5px;
            }
        }

        .bet-container {
            .multiple_box {
                font-size: 11px;
                .btn_box {
                    .el-button {
                        padding: 2px 2px;
                        font-size: 11px;
                    }
                }
            }
        }

        .position-header {
            background: transparent;
            border-top: 1px solid #787b89;
            border-bottom: 1px solid #787b89;
        }
        .el-tabs {
            .el-tabs__nav .is-top {
                height: 30px;
            }
            .el-tabs__item {
                font-size: 12px;
                padding: 0 5px !important;
                &.is-top {
                    &:first-of-type {
                        padding-left: 0px !important;
                    }

                    &:last-of-type {
                        padding-right: 0px !important;
                    }
                }
            }
        }
        .UpsAndDowns {
            margin-bottom: 5px;
            font-size: 14px;
            .up-container {
                .el-button {
                    background-color: #1dd086;
                }
            }
            .down-container {
                .el-button {
                    background-color: #e62250;
                }
            }
            .el-button {
                border-radius: 1px;
                color: #dcdfe6 !important;
                font-size: 12px;
                width: 80%;
                padding: 10px 0;
                background: transparent;
            }
        }
    }
}
</style>