<template>
    <div class="trade-history-page" style="padding-top: 1rem;">
        <!-- <div class="trade-header">
            <div>
                <el-select :placeholder="$t('main.historyPage.SelectTime')" v-model="timePeriod" class="select-styled">
                    <el-option
                            v-for="(item, i) in options"
                            :key="i"
                            :label="item.name"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>
        </div> -->
        <div class="common-header">
            <div class="back" @click="$router.back()">
                <img src="../assets/back-icon.svg" alt/>
            </div>
            <div class="content">{{$t('main.TransactionRecord.historyRecord')}}</div>
        </div>
        <tradeTable 
            :tradeItems="orderDetails" :history="true">
        </tradeTable>
        <placeholder v-if="!(orderDetails && orderDetails.length)" :icon="'el-icon-document'" 
            :message="'main.OrderRecord.NoTrades'" 
            :buttonProps="{label: 'main.OrderRecord.StartTrading', path: 'transactionPage'}"></placeholder>
    </div>
</template>

<script>
import { mapState } from "vuex";
import tradeTable from '@/components/tradeTable'
import placeholder from '@/components/placeholder'
export default {
    name: "tradeHistoryPage",
    data() {
        return {
            timePeriod: null,
            lang: "zh",
            orderDetails: [],
            options: [
                {
                    name: 'Last 30 Days',
                    value: 30
                },
                {
                    name: 'Last 2 Months',
                    value: 60
                }
            ]
        };
    },
    computed: {
    },
    created() {
        this.lang = this.$i18n.locale;
        this.getUserOrders()
    },
    mounted() {
    },
    components: {
        tradeTable,
        placeholder
    },
    methods: {
        getUserOrders (){
            // 获取持仓订单数据
            let token = this.$common.getCookie("token");
            if (!token) {
                return false;
            }
            this.$http.getOrderData().then(res => {
                if (res) {
                    let arr = res.data.data;
                    this.orderDetails = arr.historyList.reverse()
                }
            });
        },
        changeLangEvent() {
            if (this.lang === "zh") {
                this.lang = "en";
            } else {
                this.lang = "zh";
            }
            this.$i18n.locale = this.lang;
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
    }
};
</script>


<style lang="scss" scoped>
    .trade-history-page {
        width: 600px;
        margin: auto;
        padding-bottom: 70px; 
        .trade-header {
            color: #DDDEE5;
            font-size: 18px;
            display: flex;
            justify-content: space-between;
            padding: 15px 10px 0;
            .index {
                padding: 5px 0;
            }
        }

        @media screen and (max-width: 768px) {
            width: 100%;
        }
    }

</style>