<template>

    <div id="header">
        <!-- <div class="header mobile_header_box" v-if="transactionPageHeader">
            <div class="return">
                <img src="../assets/back-icon.svg" alt />
            </div>
            <div class="flex-spacer"></div>
            <div>{{chartData.showCode}}</div>
            <div class="flex-spacer"></div>
            <div class="star">
                <i
                    class="el-icon-star-on pointer hoverColor"
                    :class="{'isCollection' : (LightUp == 1)}"
                    @click="Favorites"
                    style="font-size:32px"
                ></i>
                <i class="icon-clock" style="margin-left: 10px;" @click="routerPath('/trade-history')"></i>
            </div>
        </div> -->

        <div class="header header_box flex_xc">
            <div class="header-left">
                <div class="header-logo">
                    <img src="../assets/okbix-logo-small.png" alt="OKBix" @click="routerPath('/')" />
                </div>
                <div class="btn_box flex_c_c">
                    <el-button size="medium" @click="routerPath('/transactionPage')">
                        <span v-text="$t('main.nav.transaction')"></span>
                    </el-button>
                    <el-button size="medium" @click="getTutorial">{{$t('main.nav.Tutorial')}}</el-button>
                    <el-button
                        size="medium"
                        @click="routerPath('/assets')"
                    >{{$t('main.nav.Finance')}}</el-button>
                    <el-button
                        size="medium"
                        @click="routerPath('/assets/deposit')"
                    >{{$t('main.nav.Deposit')}}</el-button>
                </div>
            </div>
            <div class="header-right flex_c_c">
                <div class="r_btn flex_yc">
                    <div class="National_China" v-if="this.lang !== 'zh'" @click="changeLangEvent"></div>
                    <div class="National_USA" v-else @click="changeLangEvent"></div>
                    <el-button
                        size="medium"
                        @click="changeLangEvent"
                    >{{$t('main.nav.LanguageaSwitching')}}</el-button>
                    <el-input
                        v-model.trim="searchText"
                        class="search-bar"
                        size="medium"
                        :placeholder="$t('main.nav.SearchText')"
                        clearable
                        style="max-width: 200px; width: 70%; margin-right:20px;"
                        @input="iconSearch"
                    ></el-input>
                </div>
                <div v-if="pageToken" class="r_user flex_c_c">
                    <img
                        class="user-pic"
                        src="../assets/eayon.jpg"
                        alt
                        @click="jumpPath('account')"
                    />
                    <div class="header-user-info">
                        <div class="user-name">{{userInfo.username}}</div>
                        <el-dropdown class="available-funds" @command="loginOut">
                            <span class="el-dropdown-link">
                                {{$t('main.AccountPage.AccountBalance')}}：{{userInfo.coinAnalog / 100 || 0}}
                                <i
                                    class="el-icon-arrow-down el-icon--right"
                                ></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item
                                    command="available"
                                >{{$t('main.AccountPage.AccountBalance')}}：{{userInfo.coinAnalog / 100 || 0}}</el-dropdown-item>
                                <el-dropdown-item command="deposit">{{$t('main.nav.Deposit')}}</el-dropdown-item>
                                <el-dropdown-item command="settings">{{$t('main.nav.Settings')}}</el-dropdown-item>
                                <el-dropdown-item
                                    command="loginOut"
                                >{{$t('main.AccountPage.LogOut')}}</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </div>
                <div v-else class="r_user flex_c_c">
                    <img class="user-pic" src="../assets/unknown-profile.png" alt />
                    <router-link
                        class="login"
                        :to="{name: 'login', query: {redirect: 'transactionPage'}}"
                    >{{$t('main.nav.Login')}}</router-link>
                    <div class="separator">{{$t('main.nav.Or')}}</div>
                    <router-link
                        class="signup"
                        :to="{name: 'register', query: {redirect: 'transactionPage'}}"
                    >{{$t('main.nav.Signup')}}</router-link>
                </div>
            </div>
        </div>
        <div class="mobile-show">
            <div class="mobile-nav-buttons">
                <div class="icon" v-bind:class="{active: path === '/'}" @click="routerPath('/')">
                    <div class="icon-img home"></div>
                    <div>{{$t('main.nav.Home')}}</div>
                </div>
                <div class="icon" v-bind:class="{active: path === '/orders'}" @click="routerPath('/orders')">
                    <div class="icon-img orders"></div>
                    <div>{{$t('main.nav.Orders')}}</div>
                </div>
                <div class="icon" v-bind:class="{active: path === '/transactionPage'}" @click="routerPath('/transactionPage')">
                    <div class="icon-img trade"></div>
                    <div>{{$t('main.nav.Trade')}}</div>
                </div>
                <div class="icon" v-bind:class="{active: path === '/assets'}" @click="routerPath('/assets')">
                    <div class="icon-img assets"></div>
                    <div>{{$t('main.nav.Assets')}}</div>
                </div>
                <div class="icon" v-bind:class="{active: path === '/account'}" @click="routerPath('/account')">
                    <div class="icon-img account"></div>
                    <div>{{$t('main.nav.Accounts')}}</div>
                </div>
            </div>
        </div>
        <Tutorial v-if="isShow" @shutDown="isShow = false" />
    </div>
</template>

<script>
import { mapState } from "vuex";
import Tutorial from "./Tutorial";
export default {
    name: "home",
    data() {
        return {
            dataList: [],
            path: this.$router.history && this.$router.history.current ? this.$router.history.current.path : '',
            searchText: "",
            searchFun: null,
            lang: "zh",
            timer: null,
            pageToken: null,
            selectedMenu: "",
            getNum: true,
            isShow: false,
            collection: [],
            LightUp: 0
        };
    },
    components: {
        Tutorial
    },
    computed: {
        ...mapState({
            // 币种列表
            cloneIconDetailList: state => state.cloneIconDetailList,
            userInfo: state => state.userInfo,
            chartData: state => state.chartData,
            SuspensionEndTime: state => state.SuspensionEndTime
        }),
        transactionPageHeader() {
            if (this.$route.name == "transactionPage") {
                return true;
            }
        },
        loginStatus() {
            return this.$common.getCookie("token");
        }
    },
    created() {
        this.lang = this.$i18n.locale;
        // 优先拿缓存里的语言类型
        const lang = this.$common.getCookie("local-lang");
        if (lang) {
            this.lang = lang;
            this.$i18n.locale = lang;
        }
        // 搜索防抖
        this.searchFun = this.$common.debounce(this.filterIconData, 500);
        // 登录状态
        this.pageToken = this.$common.getCookie("token");
        // 每5秒获取一次用户信息
        this.getUserDetail();
        this.getData();
        this.timer = setInterval(() => {
            this.getUserDetail();
        }, 5000);
    },
    watch: {
        chartData: function() {
            if (this.getNum) {
                this.getOddsTion();
            }
            this.getNum = false;
        },
        SuspensionEndTime: function() {
            this.getOddsTion();
        }
    },
    methods: {
        Favorites() {
            let msg = this.chartData;
            let obj = {
                market: msg.market,
                code: msg.code,
                status: this.LightUp
            };
            let message = this.$t("main.tip.Collectionsuccess");
            if (this.LightUp == 1) {
                message = this.$t("main.tip.Cancelsuccess");
            }
            this.$http.Collection(obj).then(res => {
                if (res) {
                    this.$notify({
                        title: this.$t("main.tip.success"),
                        message: message,
                        type: "success"
                    });
                    this.getData()
                }
            });
        },
        getData() {
            this.LightUp = 0;
            this.$http.currencyForm().then(res => {
                if(!(res && res.data))
                    return;
                let result = res.data.data;
                this.dataList = result;
                result.forEach(msg => {
                    if (!msg.collec) {
                        msg.collec = 0;
                    }
                });
                let collecList = [];
                let token = this.$common.getCookie('token')
                if(!token){
                    return false
                }
                
                this.$http.userDetails().then(res => {
                    if (res) {
                        collecList = res.data.data.favorite;
                        this.dataList.forEach(item => {
                            collecList.forEach(items => {
                                if (item.code == items.code) {
                                    item.collec = items.status;
                                }
                            });
                        });
                        this.collection = this.dataList.filter(
                            v => v.collec == 1
                        );
                        if (this.collection.length > 1) {
                            this.collection.forEach(item => {
                                if (item.code == this.chartData.code) {
                                    this.LightUp = 1;
                                }
                            });
                        }
                    }
                });
            });
        },
        getTutorial() {
            this.$common.setCookie("dialogVisible", 1);
            this.isShow = true;
        },
        getOddsTion() {
            let oddsObj = this.chartData;
            let obj = {
                market: oddsObj.market,
                code: oddsObj.code
            };
            this.$http.ProductList(obj).then(res => {
                let Index = this.$store.state.chartData.code;
                this.$store.state.fallOdds = res.data.data[Index].down;
                this.$store.state.riseOdds = res.data.data[Index].up;
                let dataMap = res.data.data;
                Object.keys(dataMap).forEach(key => {
                    this.$store.state[key] = dataMap[key];
                });
            });
        },
        updateMenu(selectedItem) {
            this.selectedMenu = selectedItem;
        },
        getUserDetail() {
            let token = this.$common.getCookie("token");
            this.pageToken = token;
            // 用户未登录就不去调接口
            if (!token) {
                clearInterval(this.timer);
                return false;
            }
            this.$store.dispatch("getUserDetail");
        },
        changeLangEvent() {
            if (this.lang === "zh") {
                this.lang = "en";
            } else {
                this.lang = "zh";
            }
            this.$common.setCookie("local-lang", this.lang);
            this.$i18n.locale = this.lang;
        },
        routerPath(route) {
            if(route === this.part) {
                return;
            }
            this.path = route;
            this.$router.push(route);
        },
        iconSearch(e) {
            this.searchFun();
        },
        // 本地搜索
        filterIconData() {
            let returnFilterIconData = this.cloneIconDetailList.filter(v =>
                v.code.toUpperCase().includes(this.searchText.toUpperCase())
            );
            this.$store.dispatch("setIconDetailList", returnFilterIconData);
        },
        loginOut(command) {
            if (command === "loginOut") {
                this.$http.loginOut().then(() => {
                    this.$common.removeCookie("token");
                    this.pageToken = null;
                    this.$message(this.$t("main.tip.Logoutsuccessful"));
                });
            } else if (command === "deposit") {
                this.jumpPath("deposit");
            } else if (command === "settings") {
                this.jumpPath("account");
            } else if (command === "available") {
                this.jumpPath("account");
            }
        },
        jumpPath(name) {
            this.$router.push({
                name: name
            });
        }
    },
    beforeDestroy() {
        // 清除定时器
        clearInterval(this.timer);
    }
};
</script>

<style lang="scss">
.home {
    .header {
        width: 100%;
        background-color: #191c25;
        font-family: "Roboto", sans-serif;
        line-height: 45px;
        color: #dddee5;
    }
    .isCollection {
        color: #ffc000;
    }
    .header_box {
        height: 55px;

        .el-button {
            background-color: transparent;
            border: none;
            color: #dddee5;
            font-size: 13px;
            margin: 0;
            &:active {
                background-color: #485371;
            }
        }

        .header-left {
            width: 50%;
            max-width: 550px;
            display: flex;
            justify-content: space-evenly;
            padding-right: 30px;
            .header-logo {
                margin-left: 12%;
                font-size: 50px;
                img {
                    margin-top: 13px;
                    max-height: 25px;
                    cursor: pointer;
                }
            }
            .btn_box {
                width: 42%;
            }
        }
        .header-right {
            width: 50%;
            max-width: 600px;
            padding-left: 20px;
            margin-right: 5%;

            .National_China,
            .National_USA {
                width: 40px;
                height: 20px;
                cursor: pointer;
            }
            .National_China {
                background: url("../assets/China.jpg") no-repeat;
                background-size: cover;
            }
            .National_USA {
                background: url("../assets/USA.jpg") no-repeat;
                background-size: cover;
            }
            .user-pic {
                max-height: 38px;
                border-radius: 50%;
                margin-right: 15px;
            }
            .r_user {
                white-space: nowrap;
                padding-right: 17px;
                a {
                    font-size: 13px;
                    &.login {
                        color: #dddee5;
                        text-decoration: none;
                    }
                    &.signup {
                        color: #04affe;
                        text-decoration: none;
                    }
                }
                .separator {
                    color: #787b89;
                    font-size: 13px;
                    padding: 0 10px;
                }
            }
            .search-bar.el-input {
                margin-left: 10px;
                input {
                    border: none;
                    border-radius: 0;
                    border-bottom: 1px solid #485371;
                    background-color: transparent;
                    height: 27px;
                    padding: 0 5px;
                    font-size: 13px;

                    &::-webkit-input-placeholder {
                        /* Chrome/Opera/Safari */
                        color: #485371;
                    }
                    &::-moz-placeholder {
                        /* Firefox 19+ */
                        color: #485371;
                    }
                    &:-ms-input-placeholder {
                        /* IE 10+ */
                        color: #485371;
                    }
                    &:-moz-placeholder {
                        /* Firefox 18- */
                        color: #485371;
                    }
                }
            }
            .header-user-info {
                margin: 2px 0;
                line-height: 12px;
                .user-name {
                    margin: 3px 0;
                    padding-left: 8px;
                    font-size: 12px;
                }
            }

            .available-funds.el-dropdown {
                span {
                    color: #787b89;
                    border: 1px solid #787b89;
                    border-radius: 20px;
                    background-color: transparent;
                    font-size: 11px;
                    padding: 1px 17px;
                    cursor: pointer;
                }
            }
        }
    }

    @media screen and (max-width: 1100px) {
        .header_box {
            .el-button {
                padding: 0 6px;
            }
        }
    }

    @media screen and (max-width: 768px) {
        .mobile-hide {
            display: none;
        }
        .mobile-show {
            display: block;
        }
        .mobile-nav-placeholder {
            height: 40px;
        }
        .mobile-nav-buttons {
            padding: 7px 0;
            background-color: #303542;
            display: flex;
            justify-content: space-evenly;
            position: fixed;
            bottom: 0;
            width: calc(100vw);
            z-index: 10;
            cursor: pointer;
            text-align: center;

            .icon {
                color: #787b89;
                text-align: center;
                font-size: 11px;
                .icon-img {
                    height: 22px;
                    width: 22px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    margin-bottom: 3px;
                    display: inline-block;
                }

                .home {
                    background-image: url("../assets/icons/mobile-nav-home.png");
                }
                .orders {
                    background-image: url("../assets/icons/mobile-nav-orders.png");
                }
                .assets {
                    background-image: url("../assets/icons/mobile-nav-assets.png");
                }
                .trade {
                    background-image: url("../assets/icons/mobile-nav-trade.png");
                }
                .account {
                    background-image: url("../assets/icons/mobile-nav-user.png");
                }

                &.active,
                &:hover {
                    color: #04affe !important;
                    .home {
                        background-image: url("../assets/icons/mobile-nav-home-blue.png") !important;
                    }
                    .orders {
                        background-image: url("../assets/icons/mobile-nav-orders-blue.png") !important;
                    }
                    .assets {
                        background-image: url("../assets/icons/mobile-nav-assets-blue.png") !important;
                    }
                    .trade {
                        background-image: url("../assets/icons/mobile-nav-trade-blue.png") !important;
                    }
                    .account {
                        background-image: url("../assets/icons/mobile-nav-user-blue.png") !important;
                    }
                }
            }
        }

        .header_box {
            display: none;
        }

    }
}
</style>
