<template>
    <div>
        <div class="mobile-show">
            <mobileHeader :returnPath="'/account'" :title="$t('main.AccountPage.ContactSupport')"></mobileHeader>
        </div> 
        <div class="contact-page asset-account-container">
            <div class="sidebar">
                <navSidebar :menuItems="menuList" :sidebarTitle="sidebarTitle" :selectedItem="selectedItem"></navSidebar>
            </div>
            <div class="main-content">
                <div class="heading">{{$t('main.AccountPage.ContactSupport')}}</div>
                <div class="body">
                    <img class="contact-img" src="../../assets/contact.png" alt="">
                    <div>{{$t('main.ContactPage.ContactMessage')}}:</div>
                    <div class="email">
                        <a href="mailto:support@okbix.com?Subject=Technical_Support" target="_top">Support@okbix.com</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import navSidebar from "@/components/navSidebar";
import { NavigationItems } from '@/common/navigation-items.js'
import mobileHeader from '@/components/mobileHeader';

export default {
    name: "contactPage",
    data() {
        return {
            isLoggedIn: false,
            userInfo: {},
            sidebarTitle: NavigationItems.userSettingsTitle,
            menuList: NavigationItems.userSettings,
            selectedItem: 'main.AccountPage.ContactSupport'
        };
    },
    computed: { },
    created() {
        let token = this.$common.getCookie('token');
        if(token) {
            this.getUserAccount()
        }
    },
    mounted() {

    },
    components: {
        navSidebar,
        mobileHeader
    },
    methods: {
        getUserAccount (){
            this.$http.userDetails().then((res) => {
                this.isLoggedIn = true;
                if(res && res.data){
                    this.userInfo = res.data.data;
                }
            });
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
    }
};
</script>

<style lang="scss">
    .contact-page {
        .body {
            font-size: 14px;
            text-align: center;
            color: #787B89;

            .contact-img {
                height: 70px;
                margin-bottom: 15px;
            }

            .email {
                margin-top: 25px;
                a {
                    color: #04affe;
                }
            }
        }
        
        @media screen and (max-width: 768px) {

        }
    }
    
</style>