import * as types from './mutation_type'
import api from '../api'

export default {
    setIconDetailList({commit}, query) {
        commit(types.ICONDETAILLIST, query);
    },
    setCloneIconDetailList({commit}, query) {
        commit(types.SETCLONEICONDETAILLIST, query);
    },
    setSuspensionEndTime({commit}, query) {
        commit(types.SUSPENSIONENDTIOME, query);
    },
    getUserDetail({commit}, query){
        api.userDetails().then((res) => {
            if(res){
                commit(types.SETUSERNAME, res);
            }
        })
    }
}