<template>
    <div>
        <div class="mobile-show">
            <mobileHeader :returnPath="'/transactionsPage'" :title="$t('main.nav.Orders')"></mobileHeader>
        </div> 
        <div class="orders-page">
            <div class="common-header">
                <div class="content">{{$t('main.TransactionRecord.currentOrder')}}</div>
            </div>
            <tradeTable 
                @getUserOrders="getUserOrders"
                :tradeItems="orderDetails">
            </tradeTable>
            <placeholder v-if="!(orderDetails && orderDetails.length)" :icon="'el-icon-s-data'" 
                :message="'main.OrderRecord.NoCurrentOrders'"
                :buttonProps="{label: 'main.OrderRecord.StartTrading', path: 'transactionPage'}"></placeholder>
        </div>
    </div>
    
</template>

<script>
import { mapState } from "vuex";
import mobileHeader from '@/components/mobileHeader';
import tradeTable from '@/components/tradeTable'
import placeholder from '@/components/placeholder'
import SockJS from "sockjs-client";
import Stomp from "stompjs";
export default {
    name: "ordersPage",
    data() {
        return {
            lang: "zh",
            orderDetails: [],
            stompClient: null
        };
    },
    computed: {
        ...mapState({
            allIconNowPrice: state => state.allIconNowPrice
        })
    },
    created() {
        this.lang = this.$i18n.locale;
        this.getUserOrders()
    },
    mounted() {
    },
    components:  {
        tradeTable,
        mobileHeader,
        placeholder
    },
    methods: {
        getUserOrders (){
            // 获取持仓订单数据
            let token = this.$common.getCookie("token");
            if (!token) {
                return false;
            }
            this.$http.getOrderData().then(res => {
                if (res) {
                    let arr = res.data.data;
                    // 持仓数据存在并且没有websocket连接，就重新连接，获取实时价格
                    if (arr.holdingList.length && !this.stompClient) {
                        this.initSocketJs();
                    }

                    // 将开始时间与结束时间丢进去
                    let allOrderTimeStampData = this.$common.getCookie('allOrderTimeStampData')
                    if(allOrderTimeStampData && allOrderTimeStampData !== '{}'){
                        let holdingList = [...arr.holdingList]
                        holdingList.forEach(v => {
                            v.nowPrice = null
                            if(JSON.parse(allOrderTimeStampData)[v.tradeId]){
                                v.initTimeStamp = JSON.parse(allOrderTimeStampData)[v.tradeId].initTimeStamp
                                v.endTimeStamp = JSON.parse(allOrderTimeStampData)[v.tradeId].endTimeStamp
                            }
                        })
                        arr.holdingList = holdingList
                    }else{
                        // 现价占个坑
                        arr.holdingList.forEach(v => v.nowPrice = null)
                    }
                    // 后端没按最新下单时间返回，前端排序一下
                    arr.holdingList.reverse();
                    this.orderDetails = arr.holdingList
                    // 把lastMinuteList存起来，用于图表结果展示
                    this.$store.commit(
                        "setLastMinuteList",
                        res.data.data.lastMinuteList
                    );
                }
            });
        },
        initSocketJs() {
            this.connection();
        },
        connection() {
            // 建立连接对象
            let socket = new SockJS(this.$base.websocketUri);
            // 获取STOMP子协议的客户端对象
            this.stompClient = Stomp.over(socket);
            this.stompClient.debug = null;
            this.stompClient.connect(
                {},
                data => {
                    this.stompClient.subscribe("/pic/all", response => {
                        this.$store.commit(
                            "setAllIconNowPrice",
                            JSON.parse(response.body)
                        );
                        this.orderDetails.forEach(v => {
                            if (this.allIconNowPrice) {
                                v.nowPrice = this.allIconNowPrice[v.marketCode];
                            }
                        });
                    });
                },
                err => {
                    // 连接发生错误时的处理函数
                    console.log("失败");
                    console.log(err);
                }
            );
        },
        disconnect() {
            if (this.stompClient) {
                this.stompClient.disconnect();
            }
        },
        changeLangEvent() {
            if (this.lang === "zh") {
                this.lang = "en";
            } else {
                this.lang = "zh";
            }
            this.$i18n.locale = this.lang;
        },
        routerPath(route) {
            this.$router.push(route);
        }
    },
    beforeDestroy (){
        this.disconnect();
    }
};
</script>


<style lang="scss" scoped>
    .orders-page {
        width: 600px;
        margin: auto;
        padding-bottom: 70px; 
        
        @media screen and (max-width: 768px) {
            width: 100%;
            table {
                margin-top: 30px;
            }
        }
    }
</style>