import * as types from './mutation_type'
import common from '../utils/common'
export default {
    [types.ICONDETAILLIST](state, data) {
        state.iconDetailList = data;
    },
    [types.SETCLONEICONDETAILLIST](state, data) {
        state.cloneIconDetailList = data;
    },
    [types.SETUSERNAME](state, data) {
        state.userInfo = data.data.data;
    },
    [types.SUSPENSIONENDTIOME](state, data) {
        state.SuspensionEndTime = data;
    },
    setChartData (state, data){
        state.chartData = data
    },
    // 把每一个下单的点保存到currentBuyInfo中
    // 现在保存的下单标记根据币种分别保存到不同的对象中
    setCurrentBuyInfo (state, data){
        let obj = {
            name: '下单标记',
            coord: [data.time, Number(data.price)],
            value: data.buyPrice,
            itemStyle: {
                normal: {color: '#ff9100'}
            },
            initTimestamp: data.initTimeStamp,
            endTimeStamp: data.endTimeStamp
        }
        obj.itemStyle.normal.color = data.buyStatus === 1 ? '#1dd086' : '#e62250'
        if(!state.currentBuyInfo[`${data.chartPeriod}-${data.market}-${data.code}`]){
            state.currentBuyInfo[`${data.chartPeriod}-${data.market}-${data.code}`] = []
        }
        state.currentBuyInfo[`${data.chartPeriod}-${data.market}-${data.code}`].push(obj)
        common.setCookie('currentBuyInfo', JSON.stringify(state.currentBuyInfo))
    },
    // 把浏览器中保存的currentBuyInfo重新保存到vuex中
    setCacheCurrentBuyInfo (state, data){
        state.currentBuyInfo = data
    },
    removeCurrentBuyInfo (state, data){
        state.currentBuyInfo.pop()
    },
    setLastMinuteList (state, data){
        state.lastMinuteList = data
    },
    setHoldingList (state, data){
        state.holdingList = data
    },
    setSceenWidth (state, data){
        state.sceenWidth = data
    },
    setIconInfo (state, data){
        state.currentIconInfo = data
    },
    setAllIconNowPrice (state, data){
        state.allIconNowPrice = Object.assign(state.allIconNowPrice, data)
    },
    setLoading(state, data) {
        state.isLoading = data;
    },
    setChartPeriod (state, data){
        state.chartPeriod = data
    },
    // 禁止下单
    setForbidChartStatus (state, data){
        state.forbidChartStatus = data
    },
    setCloneHomeIconDetailList (state, data){
        state.cloneHomeIconDetailList = data
    }
}