<template>
    <div>
        <div class="mobile-show">
            <mobileHeader :showHistory="true" :title="$t('main.navSidebar.Assets')"></mobileHeader>
        </div> 
        <div class="asset-account-container asset-page">
            <div class="sidebar">
                <navSidebar :menuItems="menuList" :sidebarTitle="sidebarTitle" :selectedItem="selectedItem"></navSidebar>
            </div>
            <div class="main-content">
                <div class="heading">{{$t('main.assetsPage.AssetsOverview')}}</div>
                <div class="asset-info">
                    <div class="asset-label">
                        {{$t('main.assetsPage.AssetsValue')}} (USD) <i class="el-icon-view" @click="toggleVisible"></i>
                    </div>
                    <div class="asset-amount">
                        <span v-if="isViewBalance">{{userInfo.coinAnalog / 100 || 0}}</span>
                        <span v-else>{{balanceStar}}</span>
                    </div>
                    <div class="asset-actions">
                        <button @click="routerPath('deposit')">{{$t('main.navSidebar.Deposit')}}</button>
                        <button @click="routerPath('withdraw')">{{$t('main.navSidebar.Withdraw')}}</button>
                    </div>
                </div>
                <div class="asset-table" v-if="assetsDetails && assetsDetails.length">
                    <table>
                        <thead class="mobile-hide">
                            <tr>
                                <th>{{$t('main.assetsPage.Asset')}}</th>
                                <th>{{$t('main.assetsPage.Balance')}}</th>
                                <th>{{$t('main.assetsPage.FrozenInTrade')}}</th>
                                <th>{{$t('main.assetsPage.Actions')}}</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, i) in assetsDetails" :key="i">
                                <td>{{item.tokenType}} <span class="source">({{item.source}})</span></td>
                                <td>{{item.amount}}</td>
                                <td class="mobile-hide">{{item.inTrade}}</td>
                                <td class="mobile-hide asset-table-actions"><a @click="routerPath('deposit')">{{$t('main.navSidebar.Deposit')}}</a> <a @click="routerPath('withdraw')">{{$t('main.navSidebar.Withdraw')}}</a></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
import navSidebar from "@/components/navSidebar";
import { NavigationItems } from '@/common/navigation-items.js'
import { mapState } from "vuex";
import mobileHeader from '@/components/mobileHeader'
export default {
    name: "accountPage",
    data() {
        return {
            sidebarTitle: NavigationItems.accountAssetsTitle,
            menuList: NavigationItems.accountAssets,
            selectedItem: 'main.navSidebar.Assets',
            isViewBalance: true,
            assetsDetails: [
                {
                    tokenType: 'USDT',
                    source: 'Tether',
                    inTrade: 1000,
                    amount: 12355.23
                }
            ]
        };
    },
    computed: {
        ...mapState({
            userInfo: state => state.userInfo
        }),
        balanceStar (){
            // 判断金钱位数 转为*展示 有小数点就减少一个*
            const realBalance = this.userInfo.coinAnalog / 100
            let balanceLen = realBalance.toString().length
            const isDecimal = realBalance.toString().includes('.')
            let result = ''
            for(let i = 0; i < balanceLen; i++){
                if(isDecimal && i === balanceLen - 1){
                    break
                }
                result += '*'
            }
            return result
        }
    },
    // beforeRouteEnter: (to, from, next) => {
    //     if(to.name === 'assets'){
    //         next(vm => {
    //             const token = vm.$common.getCookie('token')
    //             if(!token){
    //                 vm.$message(vm.$t('main.tip.firstLogin'))
    //                 vm.$router.replace({
    //                     name: from.name
    //                 })
    //             }
    //         })
    //     }else{
    //         next()
    //     }
    //     console.log(to)
    //     console.log(from)
    // },
    created() {
        this.getAssets()
    },
    mounted() {
    },
    methods: {
        // 切换账户金额是否可见
        toggleVisible (){
            this.isViewBalance = !this.isViewBalance
        },
        getAssets (){
            this.assetDetails = [
                {
                    tokenType: 'USDT',
                    source: 'Tether',
                    inTrade: 1000,
                    amount: 12355.23
                }
            ];
        },
        routerPath(route) {
            this.$router.push({
                name: route
            });
        }
    },
    components: {
        navSidebar,
        mobileHeader
    },
    beforeDestroy (){
    }
};
</script>
<style lang="scss" scoped>
    .asset-page {
       
        .main-content {
            .asset-info {
                border: 1px solid #787B89;
                padding: 30px 0 20px;
                width: 270px;
                text-align: center;
                margin-bottom: 10px;

                .asset-label {
                    font-size: 12px;
                    color: #787B89;
                    margin-bottom: 10px;
                    i {
                        color: #DDDEE5;
                        position: relative;
                        margin-left: 2px;
                        &:after{
                            content:"";
                            position:absolute;
                            border-bottom:1px solid #DDDEE5;
                            width:14px;
                            left: 11px;
                            transform: rotate(130deg);
                            transform-origin: 0% 0%;
                        }
                    }
                    
                }

                .asset-amount {
                    font-size: 20px;
                    margin-bottom: 17px;
                }

                .asset-actions {
                    button {
                        color: #787B89;
                        border: 1px solid #787B89;
                        background-color: transparent;
                        max-width: 120px;
                        width: 35%;
                        padding: 5px;
                        cursor: pointer;
                    }
                    :first-child {
                        border-right: none;
                    }
                }
            }
            .asset-table {
                table {
                    width: 100%;
                    thead tr {
                        th {
                            padding: 10px 0 7px;
                            font-size: 12px;
                            color: #787B89;
                        }
                    }
                    tbody {
                        border-top: 1px solid #787B89;
                        tr {
                            border-bottom: 1px solid #787B89;
                            text-align: center;
                            :first-child {
                                text-align: left;
                            }
                            td {

                                padding: 7px;
                                font-size: 12px;
                                color: #DDDEE5;

                                .source {
                                    color: #787B89;
                                }
                                
                                &.asset-table-actions {
                                    a {
                                        cursor: pointer;
                                        margin: 0 3px;
                                        color: #04affe;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        @media screen and (max-width: 768px) {
            .main-content {
                .asset-info {
                    width: 100%;
                    border: none;
                }
                .asset-table {
                    table {
                        tbody {
                            td {
                                padding: 14px 15px !important;
                            }
                        }
                    }
                }
            }
        }
    }
</style>