/* 
    公用方法
*/
const prefix = 'okbix-'
const current = {
    debounce: function(fn, wait = 500) {
        let timer;
        return function() {
            let args = arguments;
            if (timer) {
                clearTimeout(timer);
            }
            timer = setTimeout(() => {
                fn.apply(null, args);
            }, wait);
        };
    },
    setCookie: function (key, value) {
        if (typeof (value) === 'object' || Array.isArray(value)) {
            value = prefix + JSON.stringify(value);
        }
        sessionStorage.setItem(`${prefix}${key}`, value);
    },
    getCookie: function (key) {
        const lsVal = sessionStorage.getItem(`${prefix}${key}`);
        if (lsVal && lsVal.indexOf(prefix) === 0) {
            return JSON.parse(lsVal.split(prefix)[1]);
        } else {
            return lsVal;
        }
    },
    removeCookie: function (key) {
        sessionStorage.removeItem(`${prefix}${key}`);
    }
}

export default current