<template>
    <div id="chart-box" class="chart-box" ref="imageTofile">
        <div
            :class="{'attackEffect' : dynamicAttackEffectFlag, 'attackEffects' : fixedAttackEffectFlag}"
        ></div>
        <!-- <div class="attackEffect"></div> -->

        <div class="chart-wrapper">
            <v-chart
                style="width: 100%;height: 100%"
                ref="chart"
                :autoresize="true"
                id="chart"
                :options="option"
            />
        </div>
        <!-- 当前圆点 -->
        <div
            v-if="chartLoadingStatus"
            :style="{top:0,left:startY + 'px',transform:' translateY('+ startx +'px)'}"
            class="position-a circle"
        >
            <div class="pulse"></div>
            <div class="pulse1"></div>
        </div>
        <!-- 标志价格横线 -->
        <div
            v-if="chartLoadingStatus && sceenWidth > 768"
            :style="{left:startY + 10 + 'px', borderColor: '#494869',borderWidth: '1px',position: 'absolute',top:startx + 4 +'px',borderStyle: 'dashed',height: 0, width: lastpointValue - startY + 'px'}"
        ></div>
        <!-- 当前价格 -->
        <div
            :style="{top: startx ? startx - 7 +'px' : '-300px', right: 30 + 'px', display: sceenWidth > 768 ? 'block' : 'none'}"
            class="position-a endBorder"
        >
            <div>{{closePrice}}</div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { Event } from "@/utils/eventBus";
import ECharts from "vue-echarts";
import "echarts/lib/chart/line";
import "echarts/lib/component/markArea";
import "echarts/lib/component/markLine";
import "echarts/lib/component/markPoint";
import "echarts/lib/component/tooltip"; //引入图例组件
import SockJS from "sockjs-client";
import Stomp from "stompjs";
export default {
    name: "BussinessChart",
    components: {
        "v-chart": ECharts
    },
    props: {
        len: {
            type: Number,
            default: 60
        }
    },
    data() {
        return {
            option: {
                animation: true,
                animationDuration: 1500,
                xAxis: {
                    type: "time",
                    boundaryGap: false,
                    splitLine: {
                        show: false
                    },
                    minInterval: 1,
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    axisLabel: {
                        textStyle: {
                            color: "#a0a0c1"
                        }
                    },
                    splitNumber: 20,
                    splitArea: false //虽然不知道这个是干嘛的，但是解决了鼠标放上去的竖线和x轴刻度不对齐的问题
                },
                yAxis: {
                    type: "value",
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    nameTextStyle: {
                        fontSize: 12
                    }
                },
                grid: {
                    left: "10%"
                },
                series: [
                    {
                        data: [],
                        type: "line",
                        symbol: "none",
                        // symbol: 'image://data:image/png;base64,',
                        // symbolSize: 100,
                        itemStyle: {
                            color: "rgba(255,255,255, .8)"
                        },
                        //区域填充样式
                        lineStyle: {
                            normal: {
                                color: "#ddd" // 线条颜色
                            }
                        },
                        markLine: {
                            silent: true,
                            symbol: "none",
                            lineStyle: {
                                type: "solid",
                                width: 1
                            },
                            itemStyle: [
                                {
                                    color: {
                                        type: "linear",
                                        x: 0,
                                        y: 0,
                                        x2: 0,
                                        y2: 1,
                                        global: false // 缺省为 false
                                    }
                                }
                            ],
                            data: [
                                {
                                    lineStyle: {
                                        color: "red"
                                    },
                                    xAxis: 1573467302915
                                },
                                {
                                    lineStyle: {
                                        color: "pink"
                                    },
                                    xAxis: 1573467305414
                                }
                            ]
                        },
                        areaStyle: {
                            normal: {
                                color: {
                                    type: "linear",
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [
                                        {
                                            offset: 0,
                                            color: "#DDDDDD" // 0% 处的颜色
                                        },
                                        {
                                            offset: 1,
                                            color: "rgba(0,0,0,0)" // 100% 处的颜色
                                        }
                                    ],
                                    global: false // 缺省为 false
                                },
                                shadowColor: "rgba(0, 0, 0, 0.1)",
                                shadowBlur: 10
                            }
                        }
                    }
                ]
            },
            bar: null, //图表
            timeArr: [], //x轴数据
            showData: [], //y轴数据
            currentIndex: new Date().getSeconds(), //当前所处的秒数
            startx: 0,
            startY: 0,
            isEndBuyStatus: false, //第二个小圆点和它的价格显示标志
            lastTenpointValue: 1000, //两根竖线的坐标
            lastpointValue: -999,
            chartLoadingStatus: false, //图表加载完再显示那些圆点和价格
            needEmptyStatus: false, //是否清空数据
            timer: null,
            websockData: {
                closePrice: 0
            },
            isNeedInitStatus: true, //是否重新绘制图表（一分钟）
            splitNumber: 20, //x轴刻度
            math: 1450, //随机数
            stompClient: null, //sock连接
            websockTimer: null, // websocket重连timer
            actionTimer: null,
            initTimeStamp: 0, // 图表第一个数据的时间戳
            endTimeStamp: 0,
            lastTenData: [], //最后十秒的数据
            lastSecondData: [], //最后一秒的数据
            getBeforeEnterList: [], //图表之前的数据
            demoChartCount: 0,
            // 呼吸灯特效
            dynamicAttackEffectFlag: false,
            fixedAttackEffectFlag: false
        };
    },
    watch: {
        currentIndex: function() {
            let box = document.getElementById("chart-box");
            let iframes = document.getElementsByClassName("AudioS")[0];
            if (this.currentIndex > 39 && this.currentIndex < 50) {
                if (!iframes) {
                    var Audio = document.createElement("audio");
                    Audio.autoplay = "autoplay";
                    Audio.className = "AudioS";
                    Audio.src = require("../assets/audio/1.mp3");
                    box.appendChild(Audio);
                }
            }
            if (this.currentIndex >= 50) {
                if (iframes) {
                    box.removeChild(iframes);
                }

                // this.$store.commit("setForbidChartStatus", true);
            } else {
                // this.$store.commit("setForbidChartStatus", false);
            }
        }
    },
    computed: {
        ...mapState({
            currentBuyInfo: state => state.currentBuyInfo,
            sceenWidth: state => state.sceenWidth,
            currentIconInfo: state => state.currentIconInfo,
            chartPeriod: state => state.chartPeriod,
            chartData: state => state.chartData,
            holdingList: state => state.holdingList
        }),
        closePrice() {
            return this.websockData.closePrice || 0;
        }
    },
    created() {},
    mounted() {
        // 可能储存了币种信息，所以mount阶段不能直接调用initsocketjs方法
        let currencyType = this.$common.getCookie("CurrencyType");
        if (!currencyType) {
            this.initSocketJs();
        }
        
        this.bar = this.$refs.chart.chart;
        // // 根据屏幕宽度调整x轴分隔符数量
        const sceenWidth = document.body.clientWidth;
        this.resetSplitNumber(sceenWidth);
        window.addEventListener("resize", e => {
            this.resetSplitNumber(e.target.innerWidth);
        });
        this.howLongAction(30);
        // 图表测试数据
        this.testData()
        // 监听切换币种的动作
        Event.$on("switchIcon", () => {
            this.reloadChart();
            // loading
            const loading = this.$loading({
                lock: true,
                text: this.$t("main.tip.switchCurrency"),
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)"
            });

            setTimeout(() => {
                loading.close();
            }, 500);
        });

        // 可以立即添加标记，但不保险
        Event.$on("repaintOrderMark", () => {
            let obj = Object.assign({}, this.option);
            let finalCurrentInfo = this.setMarketPointer();
            obj.series[0].markPoint = {
                symbolSize: "40",
                label: {
                    normal: {
                        formatter: function(param) {
                            return param != null
                                ? Math.round(param.value)
                                : "";
                        }
                    }
                },
                data: finalCurrentInfo || [],
                tooltip: {
                    formatter: function(param) {
                        return (
                            param.name + "<br>" + (param.data.coord || "")
                        );
                    }
                }
            };
            this.option = obj;
        });
    },
    destroyed() {
        this.getBeforeEnterList = [];
        this.showData = [];
        this.disconnect(); //离开路由之后断开websocket连接
        Event.$off("switchIcon");
        Event.$off("repaintOrderMark");
    },
    methods: {
        // 重新加载图表，使用与切换icon品种，以及分钟周期
        reloadChart() {
            this.disconnect();
            this.isNeedInitStatus = true;
            this.initSocketJs();
        },
        // 判断用户多久没操作界面,参数为分钟数
        howLongAction(time = 2) {
            var userTime = time * 60;
            var objTime = {
                init: 0,
                time: () => {
                    objTime.init += 1;
                    if (objTime.init === userTime) {
                        // 防止下单标记的缓存过大，每次刷新就清掉它
                        this.$common.removeCookie("currentBuyInfo");
                        this.$common.removeCookie("allOrderTimeStampData");
                        this.disconnect();
                        clearInterval(this.actionTimer);
                        this.$confirm(
                            this.$t('main.tip.refreshTheInterface'),
                            this.$t('main.tip.tip'),
                            {
                                confirmButtonText: "确定",
                                type: "warning",
                                showClose: false,
                                showCancelButton: false,
                                closeOnClickModal: false
                            }
                        ).then(() => {
                            location.reload();
                        });
                    }
                },
                eventFun: () => {
                    clearInterval(this.actionTimer);
                    objTime.init = 0;
                    this.actionTimer = setInterval(objTime.time, 1000);
                }
            };

            this.actionTimer = setInterval(objTime.time, 1000);

            var body = document.querySelector("html");
            body.addEventListener("click", objTime.eventFun);
            body.addEventListener("keydown", objTime.eventFun);
            body.addEventListener("mousemove", objTime.eventFun);
            body.addEventListener("mousewheel", objTime.eventFun);
        },
        resetSplitNumber(width) {
            if (width <= 1440) {
                this.splitNumber = 10;
            }
            if (width <= 768) {
                this.splitNumber = 5;
            }
        },
        testData() {
            this.testTimer = setInterval(() => {
                this.math =
                    this.math +
                    Math.random() * Math.random() * 0.02 -
                    Math.random() * Math.random() * 0.02;
                var obj = {
                    code: "XAUUSD",
                    market: "fx",
                    period: "new",
                    price: this.math,
                    signals: "dfcf_fx_XAUUSD_001",
                    time: this.$date.formatDateTime(new Date()),
                    volume: "2.931519/0.002015"
                };
                this.handleChartWebsocketData(JSON.stringify(obj));
            }, 1000);
        },
        getBeforeEnterData() {
            // 拿之前的数据
            let query = {
                market: this.currentIconInfo.market,
                code: this.currentIconInfo.code,
                prd: this.chartPeriod
            };
            return new Promise((resolve, reject) => {
                this.$http.getBeforeEnterData(query).then(res => {
                    if (res) {
                        // 转成[timestemp, price]的格式
                        this.getBeforeEnterList = res.data.data.map(v => {
                            const formatTime = v.createdTime.replace(/-/g, "/");
                            return [Date.parse(formatTime), v.price];
                        });
                        resolve(this.getBeforeEnterList);
                    }
                });
            });
        },
        initSocketJs() {
            this.getBeforeEnterData().then(() => {
                if (this.getBeforeEnterList.length) {
                    this.skeletonInitChart();
                }
                this.connection();
                // // 断开重连机制,尝试发送消息,捕获异常发生时重连
                this.websockTimer = setInterval(() => {
                    try {
                    } catch (err) {
                        console.log("断线了: " + err);
                        this.disconnect();
                        this.connection();
                    }
                }, 5000);
            });
        },
        connection() {
            this.disconnect();
            // 建立连接对象
            let socket = new SockJS(this.$base.websocketUri);
            // 获取STOMP子协议的客户端对象
            this.stompClient = Stomp.over(socket);
            let header = {
                token: this.currentIconInfo.code
            };
            this.stompClient.debug = null;
            this.stompClient.connect(
                header,
                data => {
                    this.stompClient.subscribe(
                        `/pic/${this.currentIconInfo.market}-${this.currentIconInfo.code}`,
                        response => {
                            this.handleChartWebsocketData(response.body);
                        }
                    );
                },
                err => {
                    // 连接发生错误时的处理函数
                    console.log("失败", err);
                }
            );
        },
        disconnect() {
            if (this.stompClient) {
                this.stompClient.disconnect();
            }
            if (this.testTimer) {
                clearInterval(this.testTimer);
            }
            if (this.websockTimer) {
                clearInterval(this.websockTimer);
            }
        },
        // 图表数据的处理和判断都在这里
        handleChartWebsocketData(chartData) {
            const redata = JSON.parse(chartData);
            redata.showCode = redata.code
                .replace(/^USD/, "$&/")
                .replace(/USD$/, "/$&");
            redata.price = Number(Number(redata.price).toFixed(6))
            // 把初始时间和结束时间存起来到当前chartdata，用于储存到下单标记
            redata.initTimeStamp = this.initTimeStamp
            redata.endTimeStamp = this.endTimeStamp
            // redata.time = this.$date.formatDateTime(Date.parse(redata.time))
            redata.time = redata.time.replace(/-/g, '/').replace('T', ' ').replace(/\.\d+/g, '');
            this.$store.commit("setChartData", redata);
            // 这条数据的时间戳
            let serverTime = Date.parse(redata.time);
            // 组装成这样的格式
            this.websockData = {
                server_time: redata.time,
                closePrice: redata.price
            };
            // 记录这条数据的秒数
            this.currentIndex = this.formatDate(2, serverTime);

            // 当前时间与初始时间戳的差值 
            const differenceTimestamp = serverTime - this.initTimeStamp
            
            // 时间到达这一周期倒数40秒到倒数30秒之前(-40 ~ -30)
            if (
                differenceTimestamp >= (this.len - 40) * 1000 &&
                differenceTimestamp < (this.len - 30) * 1000
            ){
                if(this.chartPeriod === this.$config.chartPeriod5M){
                    this.fixedAttackEffectFlag = false
                    this.dynamicAttackEffectFlag = true
                }
            }else{
                this.dynamicAttackEffectFlag = false
            }

            // 时间到达这一周期倒数30秒到最后一秒之前(-30 ~ 0)
            if (
                differenceTimestamp >= (this.len - 30) * 1000 &&
                differenceTimestamp < (this.len) * 1000
            ){
                if(this.chartPeriod === this.$config.chartPeriod5M){
                    this.dynamicAttackEffectFlag = false
                    this.fixedAttackEffectFlag = true
                }
            }else{
                if(this.chartPeriod === this.$config.chartPeriod5M){
                    this.fixedAttackEffectFlag = false
                }
            }

            // 时间到达这一周期倒数20秒到倒数10秒之前(-20 ~ -10)
            if (
                differenceTimestamp >= (this.len - 20) * 1000 &&
                differenceTimestamp < (this.len - 10) * 1000
            ){
                if(this.chartPeriod === this.$config.chartPeriod1M){
                    this.fixedAttackEffectFlag = false
                    this.dynamicAttackEffectFlag = true
                }
            }else{
                this.dynamicAttackEffectFlag = false
            }

            // 时间到达这一周期倒数10秒(-10 ~ 0)
            if (
                differenceTimestamp >= (this.len - 10) * 1000 &&
                differenceTimestamp <= (this.len) * 1000
            ){
                if(this.chartPeriod === this.$config.chartPeriod1M){
                    this.dynamicAttackEffectFlag = false
                    this.fixedAttackEffectFlag = true
                }
                
                // 存储最后10秒的数据
                this.lastTenData.push([
                    serverTime,
                    this.websockData.closePrice
                ]);
            }else{
                if(this.chartPeriod === this.$config.chartPeriod1M){
                    this.fixedAttackEffectFlag = false
                }
            }

            // 时间处于下一周期00秒到01秒
            if (
                differenceTimestamp >= this.len * 1000 &&
                differenceTimestamp < (this.len + 1) * 1000
            ) {
                // 下一周期开始时，查看是否有下单，拿到所有下单的market和code去请求之前的数据，作为图表结果的展示

                this.lastSecondData.push([
                    serverTime,
                    this.websockData.closePrice
                ]);
                // 00秒前的最后一个点
                let lastCutoffData = this.showData[this.showData.length - 1];
                lastCutoffData[0] = this.$date.getMinuteStartTime(serverTime);
                // 处理一下数据，将60秒以前的最后一个点，作为60秒的点展示
                this.websockData = {
                    server_time: lastCutoffData[0],
                    closePrice: lastCutoffData[1]
                };
            }

            const oneTimestamp = 60 * 1000
            // 每分钟结算一次
            if (
                differenceTimestamp % oneTimestamp >= 1000 &&
                differenceTimestamp % oneTimestamp <= 2000
            ) {
                // 结束交易，弹出交易结果
                this.$emit("endTrade", this.initTimeStamp);
                console.log('-------------------------结算了-------------------------' + new Date(serverTime))
            }

            // 时间到达下一周期01秒
            if (
                differenceTimestamp >= (this.len + 1) * 1000
            ) {
                // 重新绘画标记设为true
                this.initTimeStamp = null
                this.isNeedInitStatus = true;
            }
            if (this.isNeedInitStatus) {
                if(!this.initTimeStamp){
                    this.initTimeStamp = this.$date.getMinuteStartTime(
                        new Date(this.websockData.server_time).getTime()
                    );
                }
                this.chartLoadingStatus = false;
                // 把模拟的图表数据数量设为0
                this.demoChartCount = 0;
                this.init();
                this.chartLoadingStatus = true;
                this.isNeedInitStatus = false;
            }

            // 获取当前价格标签位置，用来定义当前价格横线的宽度
            let endBorder = document.getElementsByClassName("endBorder");

            if(endBorder && endBorder[0]) {
                this.lastpointValue = endBorder[0].offsetLeft;
            }

            this.refreshData(this.websockData);
        },
        // 图表初始化
        init() {
            this.showData = [];
            // 首先先创一个有len+1个时间戳的数组
            let showData = new Array(this.len + 1).fill(0)
            // 防止第50秒没有数据，将最后十秒数据的第一个数据变为50秒的时间
            // 这里有两个版本，一个是把最后十秒的数据丢到第二分钟继续展示，一个是只要把最后一秒钟的丢到第二分钟展示
            // 这里写了最后十秒的逻辑，只是产品想法改了，所以后面数据未放到showdata里，代码还保留，不影响
            if (this.lastTenData.length) {
                this.lastTenData[0][0] = this.initTimeStamp;
            }
            // 如果最后一秒没有数据，就把之前保存的最后十秒的数据最后一条作为图表的第一条数据
            if (!this.lastSecondData.length && this.lastTenData.length) {
                this.lastSecondData[0] = this.lastTenData[
                    this.lastTenData.length - 1
                ];
            }
            // 防止最后一秒000毫秒没有数据
            if (this.lastSecondData.length) {
                this.lastSecondData[0][0] = this.initTimeStamp;
            }

            if (this.getBeforeEnterList.length) {
                this.getBeforeEnterList[0][0] = this.initTimeStamp;
            }
            showData.forEach((v, i) => {
                showData[i] = [this.initTimeStamp + i * 1000, ""];
            });
            this.endTimeStamp = showData[showData.length - 1][0]
            // 做个处理，因为之前发现bug，拿之前数据时发现传来的数据时间是散乱的，导致画图不正确，所以这里排序一下
            let otherData = [
                ...this.lastSecondData,
                ...this.getBeforeEnterList
            ];
            let sortArrData = otherData.sort((a, b) => {
                return a[0] - b[0];
            });
            // 合并最后一秒的数据,以及图表之前的数据
            showData = [...showData, ...sortArrData];
            this.showData = showData
            // 合并完存储的数据就清空
            this.lastTenData = [];
            this.lastSecondData = [];
            this.getBeforeEnterList = [];
            // 最后一秒的时候通知持仓组件刷新
            let suspensionEndTime = this.showData[this.len][0];
            this.$store.dispatch("setSuspensionEndTime", suspensionEndTime);
            this.drawChart();
        },
        // 这个方法用于拿到之前数据后，先画一个基本的图表，这样第一次刷新后图表会感觉加载的更快一些
        skeletonInitChart() {
            this.showData = [];
            this.showData = new Array(this.len + 1).fill(0);
            this.initTimeStamp = this.$date.getMinuteStartTime(
                this.getBeforeEnterList[0][0]
            );
            // this.showData.forEach((v, i) => {
            //     if (!this.initTimeStamp) {
            //         return false;
            //     }
            //     this.showData[i] = [this.initTimeStamp + i * 1000, ""];
            // });
            // this.endTimeStamp = this.showData[this.showData.length - 1][0]
            // this.drawChart();
        },
        // 获取时间时分秒数
        formatDate(type, timeStamp) {
            let now = new Date(timeStamp);
            var hour = now.getHours(); //返回日期中的小时数（0到23）
            var minute = now.getMinutes(); //返回日期中的分钟数（0到59）
            var seconds = now.getSeconds(); //返回日期中的分钟数（0到59）
            if (type === 0) {
                return hour;
            } else if (type === 1) {
                return minute;
            } else if (type === 2) {
                return seconds;
            }
        },
        // 刷新图表
        refreshData(websockData) {
            this.showData.push([
                websockData.server_time,
                websockData.closePrice
            ]);
            this.drawChart();
        },
        // 图表倒数多少秒的数据
        getDeadLineData(s) {
            return this.showData[this.len - s]
                ? this.$date.formatDateTime(this.showData[this.len - s][0])
                : "";
        },
        // 截止线option
        getDeadLineObj(color, data) {
            return {
                lineStyle: {
                    color: {
                        type: "linear",
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [
                            {
                                offset: 0,
                                color: "#1b1b2e" // 0% 处的颜色
                            },
                            {
                                offset: 1,
                                color: color // 100% 处的颜色
                            }
                        ]
                    }
                },
                label: {
                    show: true,
                    normal: {
                        show: true,
                        textStyle: {
                            fontSize: 12,
                            color: "#439957"
                        },
                        formatter: function(params) {
                            return params.value.split(" ")[1];
                        }
                    }
                },
                xAxis: data
            };
        },
        drawChart() {
            let obj = Object.assign({}, this.option);
            // 最大值最小值
            obj.yAxis.min = function(value) {
                return value.min - 0.02;
            };
            obj.yAxis.max = function(value) {
                return value.max + 0.02;
            };
            // y轴文字展示2位小数
            obj.yAxis.axisLabel = {
                margin: 2,
                formatter: function(value, index) {
                    return value.toFixed(2);
                },
                textStyle: {
                    color: "#a0a0c1"
                }
            };
            // 手机端将y轴向右移一点，防止数值显示不全
            if (this.sceenWidth <= 768) {
                obj.grid.left = "13%";
            }
            // x轴分隔符
            obj.xAxis.splitNumber = this.splitNumber;

            // 截止线
            const startLineData =
                this.chartPeriod === this.$config.chartPeriod1M
                    ? this.getDeadLineData(10)
                    : this.getDeadLineData(30);
            const startLineObj = this.getDeadLineObj("#439957", startLineData);
            const endLineData = this.getDeadLineData(0);
            const endLineObj = this.getDeadLineObj("#ca2626", endLineData);
            const markLineArr = [startLineObj, endLineObj];

            obj.series[0].markLine.data = markLineArr;

            // 鼠标放上去的提示
            obj.tooltip = {
                trigger: "axis",
                formatter: function(params) {
                    // 因为最开始弄进去了很多假数据，在这里处理鼠标移上去不显示空数据
                    let data = "";
                    params.forEach(v => {
                        if (v.data[1]) {
                            data = v.data[1];
                        }
                    });
                    const time = params[0].axisValueLabel;

                    if (data) {
                        return `<p>${time}<br>price：${data}</p>`;
                    } else {
                        return "";
                    }
                }
            };

            // 设置marketPointer
            let finalCurrentInfo = this.setMarketPointer();
            obj.series[0].markPoint = {
                symbolSize: "40",
                label: {
                    normal: {
                        formatter: function(param) {
                            return param != null
                                ? Math.round(param.value)
                                : "";
                        }
                    }
                },
                data: finalCurrentInfo || [],
                tooltip: {
                    formatter: function(param) {
                        return (
                            param.name + "<br>" + (param.data.coord || "")
                        );
                    }
                }
            };

            // 动画
            obj.series[0].animationEasing = function(k) {
                return k;
            };

            obj.series[0].animationDuration = 2000;
            obj.series[0].data = this.showData;

            this.option = obj;
            this.onClick();
        },
        setMarketPointer() {
            // 订单标记存入缓存中，刷新后重新丢进vuex中
            const buyInfoCookie = this.$common.getCookie("currentBuyInfo");
            let cacheOrderList;
            if (buyInfoCookie && buyInfoCookie !== "{}") {
                cacheOrderList = JSON.parse(buyInfoCookie);
            }
            if (
                JSON.stringify(this.currentBuyInfo) === "{}" &&
                cacheOrderList
            ) {
                this.$store.commit("setCacheCurrentBuyInfo", cacheOrderList);
            }
            const finalCurrentInfo = this.currentBuyInfo[
                `${this.chartPeriod}-${this.chartData.market}-${this.chartData.code}`
            ];
            return finalCurrentInfo;
        },
        onClick() {
            setTimeout(() => {
                let obj = JSON.parse(JSON.stringify(this.option)).series[0]
                    .data;

                let arr = this.bar.getViewOfSeriesModel(
                    this.bar.getModel().getSeries()[0]
                )._points;
                if (arr.length >= 62) {
                    let lastOne = arr[arr.length - 1];
                    this.startY = lastOne[0] - 5;
                    this.startx = lastOne[1] - 5;
                }
            }, 30);
        }
    }
};
</script>

