<template>
    <div ref="vaptcha" id="vaptchaContainer" style="width: 300px;height: 50px;">
        <div class="vaptcha-init-main">
            <div class="vaptcha-init-loading">
            </div>
        </div>
    </div>
</template>

<script>
const extend = function(to, _from) {
    for (const key in _from) {
        to[key] = _from[key];
    }
    return to;
};
export default {
    props: {
        type: {
            type: String,
            default: "click"
        },
        scene: {
            type: String,
            default: ""
        },
        vpStyle: {
            type: String,
            default: "dark"
        },
        color: {
            type: String,
            default: "#3C8AFF"
        },
        lang: {
            type: String,
            default: "zh-CN"
        }
    },
    mounted() {
        var config = extend(
            {
                vid: this.$config.vid, 
                offline_server: this.$config.key,
                container: this.$refs.vaptcha,
                style: this.vpStyle
            },
            this.$props
        );

        this.loadV2Script().then(() => {
            window.vaptcha(config).then(vaptchaObj => {
                vaptchaObj.render();
                // 去掉自带的？元素，防止打广告,因为浏览器前进后退会出现多个，我们只要关闭最后那个
                const vpAbountEl = document.getElementsByClassName('vp-about')
                const vpTipEl = document.getElementsByClassName('vp-btn-tip')
                setTimeout(() => {
                    document.getElementsByClassName(
                        "vp-about"
                    )[vpAbountEl.length - 1].style.display = "none";
                    document.getElementsByClassName(
                        "vp-btn-tip"
                    )[vpTipEl.length - 1].style.display = "none";
                });

                vaptchaObj.listen('pass', () => {
                    // 验证成功进行后续操作
                    const token = vaptchaObj.getToken()
                    this.$emit("success", token);
                })

                vaptchaObj.listen('close', function() {
                    //验证弹窗关闭触发
                })
            });
        });
    },
    methods: {
        loadV2Script() {
            if (typeof window.vaptcha === "function") {
                //如果已经加载就直接放回
                return Promise.resolve();
            } else {
                return new Promise(resolve => {
                    var script = document.createElement("script");
                    script.src = "https://v.vaptcha.com/v3.js";
                    script.async = true;
                    script.onload = script.onreadystatechange = function() {
                        if (
                            !this.readyState ||
                            this.readyState == "loaded" ||
                            this.readyState == "complete"
                        ) {
                            resolve();
                            script.onload = script.onreadystatechange = null;
                        }
                    };
                    document
                        .getElementsByTagName("head")[0]
                        .appendChild(script);
                });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.vaptcha-init-main {
    display: table;
    width: 100%;
    height: 100%;
    background-color: #3C8AFF;
}
​ .vaptcha-init-loading {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}
​ .vaptcha-init-loading > a {
    display: inline-block;
    width: 18px;
    height: 18px;
    border: none;
}
​ .vaptcha-init-loading > a img {
    vertical-align: middle;
}
​ .vaptcha-init-loading .vaptcha-text {
    font-family: sans-serif;
    font-size: 12px;
    color: #cccccc;
    vertical-align: middle;
}
</style>